import {
    PageTypeV4,
    SchemaType,
    ManifestSection,
    MANIFESTPATH,
    SchemaTag,
    DefinitionName,
    LogSeverity
} from '@sap/ux-specification-types';
import type { v4, ImportListReportV4Parameters } from '@sap/ux-specification-types';
import { DEFINITION_LINK_PREFIX, getPageTypeV4, removeEmptyStructure, getDefinitionKey } from '../../../common';
import { transferSettingsOfObject } from '../utils';
import { initI18n } from '../../../../i18n/i18n';
import i18next from 'i18next';
import { log } from '../../../../extensionLogger';
import { MetadataInstanceFactoryV4 as Factory } from '../../export/factory';
import type { MetadataInstanceInterface } from '../../../common/decoration/factory';
import type { Definition } from 'typescript-json-schema';
import { UIAnnotationTerms } from '@sap-ux/vocabularies-types/vocabularies/UI';

/**
 * Imports the content of annotation-based as well as custom columns
 * @param {ListReportConfig | AnalyticalListPageConfig} config - the configuration to be filled
 * @param {ImportListReportV4Parameters} importParameters  - object comprising all input data
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {PageTypeV4} pageType - page type
 * @param {string} routingId - ID of the given page
 * @param {string} configId - config id
 * @param {string} viewId - view id in multi view mode
 */
function transferColumns(
    config: v4.Table | v4.LRTableView,
    importParameters: ImportListReportV4Parameters,
    factory: MetadataInstanceInterface,
    pageType: PageTypeV4,
    routingId: string,
    configId?: string,
    viewId?: string
) {
    const schemaDefinitions = importParameters.jsonSchema.definitions;
    const lineItemsId = configId && configId !== `@${UIAnnotationTerms.LineItem}` ? configId : DefinitionName.LineItems;
    const lineItems = schemaDefinitions[lineItemsId] as Definition;
    if (lineItems) {
        config.columns = {};
        for (const columnKey in lineItems.properties) {
            const columnDefinition = (schemaDefinitions[lineItemsId] as Definition).properties[columnKey] as Definition;
            const schemaPropertyName = columnDefinition.$ref.split(DEFINITION_LINK_PREFIX)[1].split('<')[0];
            config.columns[columnKey] = factory.createInstance(pageType, schemaPropertyName);
            const targetAnnotation = viewId || configId;
            transferSettingsOfObject(
                config.columns[columnKey],
                importParameters.manifest,
                schemaDefinitions[schemaPropertyName] as Definition,
                routingId,
                [columnKey, targetAnnotation],
                targetAnnotation,
                importParameters.logger
            );
            removeEmptyStructure(config.columns, columnKey);
        }
        removeEmptyStructure(config, 'columns');
    } else {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOLINEITEMS')
        });
    }
}

/**
 * Import table toolbar actions of list report
 * @param {ImportListReportV4Parameters} importParameters  - object comprising all input data
 * @param {ListReportConfigV4} config - the configuration to be filled
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {SapUiAppPageV4} page - page in manifest
 * @param {PageTypeV4} pageType - page type
 */
function importActions(
    importParameters: ImportListReportV4Parameters,
    tableConfig: v4.Table | v4.LRTableView | v4.ALPTableView,
    factory: MetadataInstanceInterface,
    pageType: PageTypeV4,
    routingId: string,
    columnDefinitionName: string = DefinitionName.LineItems,
    viewId?: string
) {
    const schemaDefinitions = importParameters.jsonSchema.definitions;
    let targetAnnotation = viewId;
    if (
        !targetAnnotation &&
        [DefinitionName.LineItems, `${DefinitionName.LineItems}OfSPV::LineItem`].indexOf(columnDefinitionName) === -1
    ) {
        targetAnnotation = `@${UIAnnotationTerms.LineItem}#${columnDefinitionName.split('::').pop()}`;
    }
    const actionsDefinition = schemaDefinitions[`${DefinitionName.Actions}<${columnDefinitionName}>`];
    if (typeof actionsDefinition === 'object') {
        for (const actionKey in actionsDefinition.properties) {
            const actionDefinition = actionsDefinition.properties[actionKey];
            if (typeof actionDefinition === 'object' && actionDefinition.$ref) {
                const definitionKey = getDefinitionKey(actionDefinition.$ref);
                const schemaPropertyName = definitionKey.split('<')[0];
                tableConfig.toolBar.actions[actionKey] = factory.createInstance(pageType, schemaPropertyName);

                transferSettingsOfObject(
                    tableConfig.toolBar.actions[actionKey],
                    importParameters.manifest,
                    schemaDefinitions[definitionKey] as Definition,
                    routingId,
                    [actionKey, targetAnnotation]
                );
                removeEmptyStructure(tableConfig.toolBar.actions, actionKey);
            }
        }
    }
}

function transferToolBar(
    importParameters: ImportListReportV4Parameters,
    config: v4.ListReportConfigV4 | v4.Table,
    factory: MetadataInstanceInterface,
    pageType: PageTypeV4,
    routingId: string,
    columnDefinitionName: string = DefinitionName.LineItems,
    viewId?: string
) {
    const toolbarInstance = factory.createInstance(pageType, DefinitionName.ToolBar, {
        actions: {}
    });
    let table;
    if (!viewId && config && 'table' in config) {
        table = config.table;
    } else {
        table = config;
    }
    table.toolBar = toolbarInstance;

    transferSettingsOfObject(
        table.toolBar,
        importParameters.manifest,
        importParameters.jsonSchema.definitions[DefinitionName.ToolBarLR],
        routingId,
        []
    );
    importActions(importParameters, table, factory, pageType, routingId, columnDefinitionName, viewId);

    removeEmptyStructure(table.toolBar, 'actions');
    removeEmptyStructure(table, 'toolBar');
}

/**
 * Determines the target annotation of a list report view or defaultTemplateAnnotationPath
 * @param schemaDefinitions - app schema definitions
 * @param definitionName - current definition name
 * @returns the target annotation as string
 */
function determineTargetAnnotationOfView(schemaDefinitions: Definition, definitionName: string): string {
    const lineItemAnnotation = schemaDefinitions[definitionName]?.[SchemaTag.annotationPath];
    const navigationProperty = schemaDefinitions[definitionName]?.navigationProperty;
    let targetAnnotation = lineItemAnnotation?.substr(lineItemAnnotation.lastIndexOf('@'));
    targetAnnotation = navigationProperty ? `${navigationProperty}/${targetAnnotation}` : targetAnnotation;
    return targetAnnotation;
}

/**
 * Import chart & table view of ALP
 * @param {ImportListReportV4Parameters} importParameters  - object comprising all input data
 * @param {ListReportConfigV4} config - the configuration to be filled
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {SapUiAppPageV4} page - page in manifest
 * @param {PageTypeV4} pageType - page type
 * @param {string} routingId - page key in manifest
 */
function importALPViews(
    importParameters: ImportListReportV4Parameters,
    config: v4.ListReportConfigV4,
    factory: MetadataInstanceInterface,
    pageType: PageTypeV4,
    routingId: string
): void {
    const schemaDefinitions = importParameters.jsonSchema.definitions;
    //chart view
    config.chart = factory.createInstance(pageType, DefinitionName.ALPChart);
    const chartDefinition = schemaDefinitions[DefinitionName.ALPChart] as Definition;
    const chartAnnotationPath = chartDefinition[SchemaTag.annotationPath];
    transferSettingsOfObject(config.chart, importParameters.manifest, chartDefinition, routingId, [
        chartDefinition[SchemaTag.key],
        chartAnnotationPath?.substr(chartAnnotationPath.lastIndexOf('@'))
    ]);
    config.chart.toolBar = factory.createInstance(pageType, DefinitionName.ToolBar, { actions: {} });
    transferSettingsOfObject(
        config.chart.toolBar,
        importParameters.manifest,
        schemaDefinitions[DefinitionName.ViewChartToolBar] as Definition,
        routingId,
        []
    );
    //table view
    const tableAnnotationPath = schemaDefinitions[DefinitionName.ALPTable][SchemaTag.annotationPath];
    if (tableAnnotationPath) {
        const tableDefinition = schemaDefinitions[DefinitionName.ALPTable];
        const tableConfig = (config.table = factory.createInstance(
            pageType,
            DefinitionName.ALPTable
        ) as v4.ALPTableView);
        transferToolBar(importParameters, config, factory, pageType, routingId);
        const configId = tableAnnotationPath.substr(tableAnnotationPath.lastIndexOf('@'));
        transferSettingsOfObject(tableConfig, importParameters.manifest, tableDefinition, routingId, [
            tableDefinition[SchemaTag.key],
            configId
        ]);
        //transfer columns
        transferColumns(tableConfig, importParameters, factory, pageType, routingId, undefined, configId);
    }
}

/**
 * Method received view definition name and returns view type.
 * @param {string} definition  - definition name in schema.
 * @returns {DefinitionName | undefined} Resolved view type.
 */
function getViewTypeByDefinition(definition: string): DefinitionName | undefined {
    let viewType: DefinitionName;
    if (definition.indexOf(DefinitionName.LRTableView) > -1) {
        viewType = DefinitionName.LRTableView;
    } else if (definition.indexOf(DefinitionName.LRChartView) > -1) {
        viewType = DefinitionName.LRChartView;
    } else if (definition === DefinitionName.TableViewExtension) {
        viewType = DefinitionName.TableViewExtension;
    }
    return viewType;
}

/**
 * Import table views of list report
 * @param {ImportListReportV4Parameters} importParameters  - object comprising all input data
 * @param {ListReportConfigV4} config - the configuration to be filled
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {PageTypeV4} pageType - page type
 * @param {string} routingId - page key in manifest
 */
function importLRViews(
    importParameters: ImportListReportV4Parameters,
    config: v4.ListReportConfigV4,
    factory: MetadataInstanceInterface,
    pageType: PageTypeV4,
    routingId: string
): void {
    const tableViewConfig: v4.MultiTableModeV4 = ((config.table as v4.Table).views = factory.createInstance(
        pageType,
        DefinitionName.MultiTableModeV4
    ));
    const schemaDefinitions = importParameters.jsonSchema.definitions as Definition;

    //Transfer settings on overall views' level
    transferSettingsOfObject(
        tableViewConfig,
        importParameters.manifest,
        schemaDefinitions[DefinitionName.MultiTableModeV4],
        routingId,
        []
    );
    const properties = schemaDefinitions[DefinitionName.MultiTableModeV4].properties;
    const sortedList = Object.entries(properties).sort(
        (a, b) => a[1][SchemaTag.propertyIndex] - b[1][SchemaTag.propertyIndex]
    );
    for (const element of sortedList) {
        //process single views
        if (element[1]['$ref']) {
            const nextDefinition = element[1]['$ref'].split(DEFINITION_LINK_PREFIX)[1];
            const viewType = getViewTypeByDefinition(nextDefinition);
            const tableView = (tableViewConfig[element[0]] = factory.createInstance(pageType, viewType));
            const targetAnnotation = determineTargetAnnotationOfView(schemaDefinitions, nextDefinition);
            transferSettingsOfObject(
                tableView,
                importParameters.manifest,
                schemaDefinitions[nextDefinition],
                routingId,
                [element[0], targetAnnotation]
            );
            if (viewType === DefinitionName.LRTableView) {
                const definitionName = `${DefinitionName.LineItemsOfView}::${element[0]}`;
                transferToolBar(
                    importParameters,
                    tableView,
                    factory,
                    pageType,
                    routingId,
                    definitionName,
                    targetAnnotation
                );
                transferColumns(
                    tableView,
                    importParameters,
                    factory,
                    pageType,
                    routingId,
                    definitionName,
                    targetAnnotation
                );
            }
            if (Object.keys(tableView).length === 0) {
                delete tableViewConfig[element[0]];
            }
        }
    }
    removeEmptyStructure(config.table, 'views');
}

/**
 * Adds manifest settings to the config.json of LR or ALP V4
 * @param {ImportListReportV4Parameters} importParameters  - object comprising all input data
 * @param {ListReportConfig | AnalyticalListPageConfig} config - the configuration to be filled
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {PageTypeV4} pageType - page type
 * @param {string} routingId - page key in manifest
 * @param {SchemaType} schemaType - allows distinction of LR and ALP scenario
 */
export function addSettings(
    importParameters: ImportListReportV4Parameters,
    config: v4.ListReportConfigV4,
    factory: MetadataInstanceInterface,
    pageType: PageTypeV4,
    routingId: string,
    schemaType: SchemaType
): void {
    // Transfer page level settings with sync rules
    transferSettingsOfObject(config, importParameters.manifest, importParameters.jsonSchema, routingId, []);

    //FilterBar:
    config.filterBar = factory.createInstance(pageType, DefinitionName.FilterBar);

    transferSettingsOfObject(
        config.filterBar,
        importParameters.manifest,
        importParameters.jsonSchema.definitions[DefinitionName.FilterBar],
        routingId,
        []
    );
    //Distinguish ALP table from LR table
    if (schemaType === SchemaType.ListReport) {
        let definitionName = DefinitionName.Table;
        let tableDefinition = importParameters.jsonSchema.definitions[definitionName] as Definition;
        let columnDefinitionName;
        if (tableDefinition.$ref) {
            //(S)PV scenario ==> delegation, go to TableSPV
            definitionName = tableDefinition.$ref.split(DEFINITION_LINK_PREFIX)[1] as DefinitionName;
            tableDefinition = importParameters.jsonSchema.definitions[definitionName] as Definition;
            columnDefinitionName = (tableDefinition.properties.columns as Definition).$ref.split(
                DEFINITION_LINK_PREFIX
            )[1];
        }
        config.table = factory.createInstance(pageType, DefinitionName.Table);
        const targetAnnotation = determineTargetAnnotationOfView(
            importParameters.jsonSchema.definitions,
            definitionName
        );
        transferSettingsOfObject(config.table, importParameters.manifest, tableDefinition, routingId, [
            definitionName,
            targetAnnotation
        ]);
        //transfer toolbar
        transferToolBar(importParameters, config, factory, pageType, routingId, columnDefinitionName);
        //transfer columns
        transferColumns(
            config.table,
            importParameters,
            factory,
            pageType,
            routingId,
            columnDefinitionName,
            targetAnnotation
        );
        importLRViews(importParameters, config, factory, pageType, routingId);
    } else if (schemaType === SchemaType.AnalyticalListPage) {
        importALPViews(importParameters, config, factory, pageType, routingId);
    }
}

/**
 * Creates the configuration file content for a list report V4
 * @param {ImportListReportV4Parameters} importParameters  - object comprising all input data
 *
 * @returns {ListReportConfig} - the configuration (JSON) for the list report
 */
export function createListReportConfig(
    importParameters: ImportListReportV4Parameters,
    schemaType: SchemaType
): v4.ListReportConfigV4 | undefined {
    // Initialize i18next
    initI18n();
    if (!importParameters.jsonSchema) {
        return;
    }
    if (!importParameters.manifest[ManifestSection.ui5] || !importParameters.manifest[ManifestSection.ui5]['routing']) {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOROUTING'),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.ui5]
            }
        });
        return;
    }
    const v4Pages = importParameters.manifest[ManifestSection.ui5]['routing']['targets'];
    if (!v4Pages) {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOTARGETS'),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.ui5]
            }
        });
        return;
    }
    const factory = new Factory();
    //Instantiate Page
    const config = factory.createPageInstance(PageTypeV4.ListReport);

    for (const key in v4Pages) {
        const v4Page = v4Pages[key];
        const pageType = getPageTypeV4(v4Page);
        if ('name' in v4Page && (pageType === PageTypeV4.ListReport || pageType === PageTypeV4.AnalyticalListPage)) {
            addSettings(importParameters, config, factory, PageTypeV4.ListReport, key, schemaType);
            break;
        }
    }

    return config;
}
