import type { ObjectPageHeader, ObjectPageLayout } from '../controls';
import { v4 } from '@sap/ux-specification-types';
import { pageLayoutInformation, childId, syncRule, validity } from '../../../common/decoration/decorators';
import { getSettingsPathForPage } from '../../application';

@pageLayoutInformation({
    id: `${v4.FE_TEMPLATE_V4_OBJECT_PAGE}.view.Details`,
    pageId: v4.FE_TEMPLATE_V4_OBJECT_PAGE
})
export class ObjectPage implements v4.ObjectPageConfigV4 {
    @childId('header')
    header?: ObjectPageHeader;

    @childId('layout')
    layout?: ObjectPageLayout;

    @syncRule({
        manifest: {
            path: (entitySet: string[]): string => getSettingsPathForPage(entitySet)
        }
    })
    @validity({
        since: '1.84.0'
    })
    variantManagement?: v4.VariantManagementTypeObjectPage;

    @childId('sections')
    sections?: v4.GenericSections | v4.CustomSections;
}
