import type { v4, Manifest } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getActionsPathForOP } from '../../application';
import { addPatternForBindingChangeOfEnumOP } from '../../../common/decoration/control';

export class ObjectPageToolBarAction implements v4.ObjectPageToolBarAction {
    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    enableOnSelect?: v4.SelectType;
}

/**
 * Sync class for Position
 */
export class CustomActionPositionOP implements v4.CustomActionPositionOP {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey: string): string =>
                getActionsPathForOP(pageName, manifest, sectionId, columnKey) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    anchor?: string;
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey: string): string =>
                getActionsPathForOP(pageName, manifest, sectionId, columnKey) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    placement: v4.ActionPlacement;
}

export class CustomActionOP implements v4.CustomActionOP {
    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    text: string;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    position: CustomActionPositionOP;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    press: string;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    requiresSelection?: boolean;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    @validity({
        since: '1.84.0'
    })
    visible?: boolean;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    @validity({
        since: '1.84.0'
    })
    enabled?: boolean;
}
