import type { v4 } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getActionsPathForOP } from '../../application';
import { addPatternForBindingChangeOfEnumOP } from '../../../common/decoration/control';
import type { CustomActionPositionOP } from './ObjectPageToolBarAction';

export class FormCustomActionOP implements v4.FormCustomActionOP {
    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    text: string;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    position: CustomActionPositionOP;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    press: string;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    @validity({
        since: '1.84.0'
    })
    visible?: boolean;

    @syncRule({
        manifest: {
            path: getActionsPathForOP
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    @validity({
        since: '1.84.0'
    })
    enabled?: boolean;
}
