import type { v4 } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getSettingsPathForPage } from '../../application';

export class ObjectPageLayout implements v4.ObjectPageLayout {
    @syncRule({
        manifest: {
            path: (entitySet: string[]): string => getSettingsPathForPage(entitySet)
        }
    })
    @validity({
        since: '1.84.0'
    })
    sectionLayout?: v4.SectionLayoutType;
}
