import type { Target } from './page';
import type { Pages as PagesV2, AppSettings as AppSettingsV2 } from '../v2';
import type { Pages as PagesV4, AppSettings as AppSettingsV4 } from '../v4';
import type { FlexibleColumnLayoutType } from './types';

export enum DraftDiscardEnabledSettings {
    restricted = 'restricted'
}
export interface DraftDiscardConfirmationSettings {
    /**
     * Allows applications to turn off the discard draft confirmation popup in various scenarios
     */
    enabled: DraftDiscardEnabledSettings;
}

export enum TableColumnVerticalAlignment {
    Top = 'Top',
    Middle = 'Middle',
    Bottom = 'Bottom'
}

export enum StatePreservationMode {
    persistence = 'persistence',
    discovery = 'discovery'
}

export interface Application {
    $schema?: string;
    id?: string;
    pages?: PagesV2 | PagesV4;
    home?: string;
    target?: Target;
    settings?: AppSettingsV2 | AppSettingsV4;
}

export enum FlexibleColumnLayoutAggregations {
    BeginColumnPages = 'beginColumnPages',
    MidColumnPages = 'midColumnPages',
    EndColumnPages = 'endColumnPages'
}

export interface FlexibleColumnLayout {
    defaultTwoColumnLayoutType?: FlexibleColumnLayoutType;
    defaultThreeColumnLayoutType?: FlexibleColumnLayoutType;
}

export interface CommonAppSettings {
    /**
     * To change the application header, in your project artifacts, change the i18n property file
     * for your app under webapp/i18n/i18n.properties ->appTitle. Then, refer to the title as
     * {{appTitle}} in the sap.app section of the manifest file.
     * @i18nClassification TIT: Title of the application
     */
    title?: string;
    /**
     * To change the application header, in your project artifacts, change the i18n property file
     * for your app under webapp/i18n/i18n.properties ->appDescription. Then, refer to the description
     * as {{appDescription}} in the sap.app section of the manifest file.
     * @i18nClassification TIT: Description of the application
     */
    description?: string;
    /**
     * To enable key user adaptation for the application, set the flexEnabled property to "true".
     */
    flexEnabled?: boolean;
}
