import type { v2 } from '@sap/ux-specification-types';
import { ControlType } from '@sap/ux-specification-types';
import { syncRule, validity, buildControlIdFromParent, exportToFlexChange } from '../../../common/decoration';
import { getAppSettingOBJPathV2 } from '../../application';
import { addPatternForBindingChangeOfEnumOP } from '../../../common';
import type AvatarShape from 'sap/m/AvatarShape';

const getAvatarControlId = (baseId: string, idBreadcrumbs: string[]): string => {
    const idBreadcrumbsCopy = idBreadcrumbs.slice(0);
    idBreadcrumbsCopy[idBreadcrumbs.length - 1] = 'objectImage';
    return buildControlIdFromParent(baseId, idBreadcrumbsCopy);
};

export class ObjectPageHeader<ACT = v2.GenericActions> implements v2.ObjectPageHeader<ACT> {
    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingOBJPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.46.0'
    })
    showRelatedApps?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingOBJPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.46.0'
    })
    editableHeaderContent?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingOBJPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.48.0'
    })
    simpleHeaderFacets?: boolean;

    @syncRule({
        flex: {
            controlId: getAvatarControlId,
            controlType: (): ControlType => ControlType.Avatar,
            createFlexChange: exportToFlexChange
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    displayShape?: AvatarShape;

    actions?: ACT;
}
