export const getColumnsPathForOP = (
    pageName: string[],
    _manifest: Manifest,
    sectionId: string,
    columnKey: string
): string => {
    return getControlConfigurationPath(pageName) + `/${sectionId}/columns/${columnKey}`;
};

export const getActionsPathForOP = (
    pageName: string[],
    _manifest: Manifest,
    sectionId: string,
    columnKey: string
): string => {
    return getControlConfigurationPath(pageName) + `/${sectionId}/actions/${columnKey.replace('/', '??')}`;
};

export const getTableSettingsPathForOP = (pageName: string[], _manifest: Manifest, sectionId: string): string => {
    return getControlConfigurationPath(pageName) + `/${sectionId}/tableSettings`;
};

export const getCustomSectionPath = (
    pageName: string[],
    _manifest: Manifest,
    sectionId: string,
    pathPart?: string
): string => {
    let path = getSettingsPathForPage(pageName) + `/content/body/sections/${sectionId}`;
    if (pathPart) {
        path += '/' + pathPart;
    }
    return path;
};

export class ApplicationV4 implements Application {
    $schema?: string;
    id?: string;
    pages?: v4.Pages;
    home?: string;
    target?: Target;
    @childId('settings')
    settings?: AppSettings;
}

export class AppSettings implements v4.AppSettings {
    @syncRule({
        manifest: {
            path: (): string => 'sap.app'
        }
    })
    title?: string;

    @syncRule({
        manifest: {
            path: (): string => 'sap.app'
        }
    })
    description?: string;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5/routing/config'
        }
    })
    @validity({
        since: '1.78.0'
    })
    flexibleColumnLayout?: v4.FlexibleColumnLayoutV4;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5'
        }
    })
    @validity({
        since: '1.56.0'
    })
    flexEnabled?: boolean;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5/routing/config'
        }
    })
    viewType?: ViewTypes;

    @syncRule({
        manifest: {
            path: (): string => 'sap.ui5/routing/config'
        }
    })
    path?: string;

    @syncRule(controllerExtensions)
    controllerExtensions?: v4.ControllerExtensions;
}
