import { parseAndMergeAndConvert } from '../../common/utils';
import { generateFreestyleSchemaV4, generateListReportSchemaV4, generateObjectPageSchemaV4 } from '.';
import type { ExtensionLogger, GenerateAppSchemaParameters, FileData } from '@sap/ux-specification-types';
import { v4, FioriElementsVersion, TemplateType } from '@sap/ux-specification-types';

/**
 * Generates the app specific schema of an object page out of the generic schema for the same page.
 * Generic types are replaced by information from the app specific annotations.
 *
 * @param genericSchema - generic JSON schema of a list report
 * @param entitySetName - the base entity set of the given page
 * @param annotations - list of all annotation files (file content plus URI as identifier)
 * @param manifest - manifest.json of the app
 * @param {ExtensionLogger} logger - Logger class for logging messages
 * @param {FileData[]} fragments - list of fragment files and their content
 *
 * @returns the app specific JSON schema
 */
export function generateObjectPageInterfaceV4(
    genericSchema: object,
    entitySet: string,
    annotations: FileData[],
    manifest: object,
    logger?: ExtensionLogger,
    fragments?: FileData[]
): object {
    const serviceAVT = parseAndMergeAndConvert(annotations, logger);
    const generateParameters: GenerateAppSchemaParameters = {
        templateType: TemplateType.ListReportObjectPageV4,
        fioriElementsVersion: FioriElementsVersion.v4,
        manifest,
        fragments,
        serviceAVT,
        logger
    };
    return generateObjectPageSchemaV4(generateParameters, genericSchema, entitySet);
}

/**
 * Generates the app specific schema of a list report out of the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 * @param {TemplateType} templateType - the template type allows distinguishing LR and ALP
 * @param genericSchema - generic JSON schema of a list report
 * @param entitySetName - the base entity set of the given page
 * @param annotations - list of all annotation files (file content plus URI as identifier)
 * @param {ExtensionLogger} logger - Logger class for logging messages
 * @param manifest - manifest.json of the app
 *
 * @returns the app specific JSON schema
 */
export function generateListReportInterfaceV4(
    templateType: TemplateType,
    genericSchema: object,
    entitySetName: string,
    annotations: FileData[],
    logger?: ExtensionLogger,
    manifest?: object
): object {
    const serviceAVT = parseAndMergeAndConvert(annotations, logger);
    const entitySet = serviceAVT?.entitySets.find((es) => es.name === entitySetName);
    const generateParameters: GenerateAppSchemaParameters = {
        templateType,
        fioriElementsVersion: FioriElementsVersion.v4,
        manifest,
        serviceAVT,
        logger
    };

    const templateName =
        templateType === TemplateType.ListReportObjectPageV4 ? v4.FE_TEMPLATE_V4_LIST_REPORT : v4.FE_TEMPLATE_V4_ALP;
    return generateListReportSchemaV4(generateParameters, genericSchema, entitySet, templateName);
}

/**
 * Generates the app specific schema of a freestyle page out of the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 * @param {TemplateType} templateType - the template type allows distinguishing LR and ALP
 * @param genericSchema - generic JSON schema of a list report
 * @param annotations - list of all annotation files (file content plus URI as identifier)
 * @param {ExtensionLogger} logger - Logger class for logging messages
 * @param manifest - manifest.json of the app
 *
 * @returns the app specific JSON schema
 */
export function generateFreestyleInterfaceV4(
    templateType: TemplateType,
    genericSchema: object,
    annotations: FileData[],
    manifest?: object,
    _entitySetName?: string,
    logger?: ExtensionLogger
): object {
    const serviceAVT = parseAndMergeAndConvert(annotations, logger);
    const generateParameters: GenerateAppSchemaParameters = {
        templateType,
        fioriElementsVersion: FioriElementsVersion.v4,
        manifest,
        serviceAVT,
        logger
    };
    return generateFreestyleSchemaV4(generateParameters, genericSchema);
}
