import type { ConvertedMetadata, EntitySet, EntityType } from '@sap-ux/vocabularies-types';
import type { DataFieldAbstractTypes } from '@sap-ux/vocabularies-types/vocabularies/UI';
import { UIAnnotationTerms, UIAnnotationTypes } from '@sap-ux/vocabularies-types/vocabularies/UI';
import { getStableIdPartFromDataField } from '../../v4/utils/StableIdHelper';
import type { FacetConfig, GenerateAppSchemaParameters } from '@sap/ux-specification-types';
import {
    DefinitionName,
    FacetBase,
    FioriElementsVersion,
    PropertyName,
    SchemaTag,
    SectionType,
    UIVOCABULARY
} from '@sap/ux-specification-types';
import {
    DEFINITION_LINK_PREFIX,
    getDataFieldDescription,
    createAnnotationPath,
    findAlias,
    prepareRef,
    addDataTypeToDefinition
} from '..';
import type { Definition } from 'typescript-json-schema';

/**
 * Copies the field properties from the generic definition to the specific field definition
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @param {object} appSchema - App schema in general
 * @param {DataFieldAbstractTypes} field - field definition in parser output (AVT)
 * @param {string} fieldDefinition - the unique ID for the given field definition
 */
function copyFieldProperties(
    version: FioriElementsVersion,
    appSchema: Definition,
    field: DataFieldAbstractTypes,
    fieldDefinition: string
) {
    if (version === FioriElementsVersion.v4 && appSchema['definitions']['Field']) {
        switch (field.$Type) {
            case UIAnnotationTypes.DataField:
            case UIAnnotationTypes.DataFieldForAnnotation:
            case UIAnnotationTypes.DataFieldWithNavigationPath:
            case UIAnnotationTypes.DataFieldWithUrl: {
                (appSchema.definitions[fieldDefinition] as Definition).properties =
                    appSchema.definitions.Field['properties'];
                break;
            }
        }
    } else if (version === FioriElementsVersion.v2 && appSchema['definitions']['Field']) {
        switch (field.$Type) {
            case UIAnnotationTypes.DataField:
            case UIAnnotationTypes.DataFieldForAnnotation:
            case UIAnnotationTypes.DataFieldWithUrl: {
                (appSchema.definitions[fieldDefinition] as Definition).properties =
                    appSchema.definitions.Field['properties'];
                break;
            }
        }
    }
}

/**
 * Determines the name for a field definition in app schema
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @param fieldReferenceId - the stable ID representation of the field
 * @param {string} targetID - unique ID, identifies the parent object (form)
 * @param fieldReference - the reference to the field definition in schema
 * @returns the field definition name in schema
 */
function determineFieldDefinitionName(
    version: FioriElementsVersion,
    fieldReferenceId: string,
    targetID: string,
    fieldReference: string
): string {
    const fieldReferenceType =
        version === FioriElementsVersion.v2 && fieldReferenceId && fieldReferenceId.includes('::')
            ? fieldReferenceId.split('::')[0].replace('DataField', 'Field')
            : undefined;
    if (version === FioriElementsVersion.v2) {
        return `${fieldReferenceType}<${targetID}::${fieldReference}>`;
    } else {
        return `${targetID}::${fieldReference}`;
    }
}

/**
 * Adds a field definition to the app specific schema
 * @param {string} sectionType - prefix to distinguish Section and HeaderSection
 * @param {object} appSchema - App schema in general
 * @param {DataFieldAbstractTypes} field - field definition in parser output (AVT)
 * @param {string} targetID - unique ID, identifies the parent object (form)
 * @param {string} key - current field key
 * @param {SectionType} section - current section of the object page
 * @param {FioriElementsVersion} version - Fiori Elements version
 */
function addField(
    sectionType: SectionType,
    appSchema: Definition,
    field: DataFieldAbstractTypes,
    targetID: string,
    key: string,
    section: FacetConfig,
    version: FioriElementsVersion,
    withActions = true
): void {
    // Calculate field reference
    const fieldReferenceId = getStableIdPartFromDataField(field);
    const fieldReference =
        version === FioriElementsVersion.v2 && fieldReferenceId && fieldReferenceId.includes('::')
            ? fieldReferenceId.split('::').splice(1).join('::')
            : fieldReferenceId;
    // Determine field definition name
    const fieldDefinition = determineFieldDefinitionName(version, fieldReferenceId, targetID, fieldReference);

    // Evaluate field type, set field reference of properties of fields' or actions' definition
    if (!field || !field.$Type) {
        return;
    } else if (
        field.$Type === UIAnnotationTypes.DataFieldForAction ||
        field.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation
    ) {
        if (withActions && sectionType === SectionType.Section) {
            const actionsDefinition = prepareRef(`${sectionType}${DefinitionName.Actions}<${targetID}>`);
            const fieldInSchema = ((appSchema.definitions[actionsDefinition] as Definition).properties[fieldReference] =
                {
                    $ref: `${DEFINITION_LINK_PREFIX}${fieldDefinition}`
                });
            fieldInSchema[SchemaTag.propertyIndex] = parseInt(key);
        }
    } else {
        const fieldsDefinition = prepareRef(`${sectionType}${DefinitionName.Fields}<${targetID}>`);
        appSchema.definitions[fieldsDefinition]['properties'][fieldReference] = {
            $ref: `${DEFINITION_LINK_PREFIX}${fieldDefinition}`,
            propertyIndex: parseInt(key)
        };
    }

    // Now set up the field definition
    appSchema.definitions[fieldDefinition] = {
        type: 'object',
        properties: {},
        description: getDataFieldDescription(field, section.entityType),
        additionalProperties: false
    };
    //Add tags
    appSchema.definitions[fieldDefinition][SchemaTag.annotationPath] = `/${section.entityType.fullyQualifiedName}/${
        field.fullyQualifiedName.split(section.entityType.fullyQualifiedName)[1]
    }`;
    appSchema.definitions[fieldDefinition][SchemaTag.annotationType] = field.$Type;
    appSchema.definitions[fieldDefinition][SchemaTag.isViewNode] = true;

    addDataTypeToDefinition(appSchema.definitions[fieldDefinition] as Definition, field);

    // Copy the generic definition of field properties to the specific one
    copyFieldProperties(version, appSchema, field, fieldDefinition);
    if (field.$Type === UIAnnotationTypes.DataFieldForAction && appSchema.definitions?.['FormAction']) {
        (appSchema.definitions[fieldDefinition] as Definition).properties = (
            appSchema.definitions[DefinitionName.FormAction] as Definition
        ).properties;
    }
}

/**
 * The function determines the target ID that shall be used in the app schema for a form
 * @param section - object describing the section or facet
 * @returns the target ID (string)
 */
function determineTargetIdOfForm(section: FacetConfig) {
    let targetID: string;
    if (section.target && section.target['qualifier'] && section.entityType) {
        targetID = `${section.entityType.name}::${section.base}::${section.target['qualifier']}`;
    } else if (section.entityType) {
        targetID = `${section.entityType.name}::${section.base}`;
    } else {
        targetID = section.ID;
    }
    return targetID;
}

/**
 * Adds definitions for forms in object page sections to the app schema
 * @param {SectionType} sectionType - prefix to distinguish Section and HeaderSection
 * @param {FacetConfig} section - facet or section in the FE core model
 * @param {object} appSchemaSection - current definition of facet or section in the JSON schema
 * @param {object} appSchema - app specific schema that gets enhanced
 * @param {FioriElementsVersion} version - Fiori Elements version
 * @returns {string | undefined} target id of form
 */
export function handleForm(
    sectionType: SectionType,
    section: FacetConfig,
    appSchemaSection: Definition,
    appSchema: Definition,
    version: FioriElementsVersion,
    withActions = true
): string | undefined {
    if (!section.target) {
        return;
    }
    const targetID: string = determineTargetIdOfForm(section);
    const formDefinition = prepareRef(`${sectionType}${DefinitionName.Form}<${targetID}>`);
    const fieldsDefinition = prepareRef(`${sectionType}${DefinitionName.Fields}<${targetID}>`);
    const actionsDefinition = prepareRef(`${sectionType}${DefinitionName.Actions}<${targetID}>`);

    // Form definition
    appSchemaSection.properties['form'] = { $ref: `${DEFINITION_LINK_PREFIX}${formDefinition}` };

    if (sectionType === SectionType.Section && withActions) {
        appSchema['definitions'][formDefinition] = {
            type: 'object',
            properties: {
                actions: {
                    $ref: `${DEFINITION_LINK_PREFIX}${actionsDefinition}`
                },
                fields: { $ref: `${DEFINITION_LINK_PREFIX}${fieldsDefinition}` }
            },
            description: 'Form',
            additionalProperties: false
        };
        appSchema['definitions'][actionsDefinition] = {
            type: 'object',
            properties: {},
            description: 'Actions',
            additionalProperties: false
        };
        appSchema['definitions'][actionsDefinition][SchemaTag.isViewNode] = true;
    } else {
        appSchema['definitions'][formDefinition] = {
            type: 'object',
            properties: {
                fields: { $ref: `${DEFINITION_LINK_PREFIX}${fieldsDefinition}` }
            },
            description: 'Form',
            additionalProperties: false
        };
    }
    appSchema.definitions[formDefinition][SchemaTag.isViewNode] = true;

    if (section.entityType?.name) {
        appSchema.definitions[formDefinition][SchemaTag.annotationPath] = createAnnotationPath(
            section.entityType.fullyQualifiedName,
            section.target['term'],
            section.target['qualifier']
        );
    }

    // Fields definition
    appSchema.definitions[fieldsDefinition] = {
        type: 'object',
        properties: {},
        description: 'Fields',
        additionalProperties: false
    };
    appSchema.definitions[fieldsDefinition][SchemaTag.isViewNode] = true;
    // Add fields
    if (section.base === FacetBase.Identification) {
        for (const key in section.target) {
            const field = section.target[key];
            if (field) {
                addField(sectionType, appSchema, field, targetID, key, section, version, withActions);
            }
        }
    } else {
        for (const key in section.target['Data']) {
            const field = section.target['Data'][key];
            addField(sectionType, appSchema, field, targetID, key, section, version);
        }
    }
    return targetID;
}

/**
 * Replaces an alias in an annotation ID or reference, based on the AVT references' list
 * @param annotationIdentifier - annotation ID or reference
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 * @returns the converted string
 */
export function replaceAlias(annotationIdentifier: string, oDataServiceAVT: ConvertedMetadata): string {
    const potentialAlias = annotationIdentifier.indexOf('.') > -1 ? annotationIdentifier.split('.')[0] : undefined;
    let result = annotationIdentifier;
    if (potentialAlias && oDataServiceAVT) {
        const searchTerm = potentialAlias.replace('SAP__', '');
        const reference = oDataServiceAVT.references.find((ref) => {
            return ref.alias === searchTerm;
        });
        if (reference) {
            result = annotationIdentifier.replace(potentialAlias, reference.namespace);
        }
    }
    return result;
}

/**
 * Common routine to determine the name and the ID of a header action
 * @param action - action as given by AVT
 * @param entityType - entity type by AVT
 * @returns an object comprising action name and action id
 */
export function getActionNameAndId(action: string, entityType: EntityType): { actionName: string; actionId: string } {
    let separatorIndex = action.lastIndexOf('/');
    if (separatorIndex === -1) {
        separatorIndex = action.lastIndexOf('.');
    }
    let actionName, actionId;
    if (separatorIndex > -1) {
        actionName = action.substring(separatorIndex + 1);
        actionId = `${action.substring(0, separatorIndex)}::${actionName}`;
    } else {
        actionName = action;
        const namespace = entityType.fullyQualifiedName.split(entityType.name)[0];
        actionId = `${namespace.substring(0, namespace.length - 1)}::${actionName}`;
    }
    return { actionName, actionId };
}

/**
 * Add Object Page Footer Action Buttons to app-specific schema
 *
 * @param {object} appSchema Schema of the app
 * @param {EntityType} entityType - current entity type
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 */
export function addFooterActions(appSchema: Definition, generateParameters: GenerateAppSchemaParameters): void {
    appSchema.definitions[DefinitionName.ObjectPageFooter] = JSON.parse(
        JSON.stringify(appSchema.definitions[`${DefinitionName.ObjectPageFooter}<ObjectPageFooterActions>`])
    );
    // Change reference of footer
    appSchema.properties['footer'] = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.ObjectPageFooter}`
    };
    // Add action to configuration
    (appSchema.definitions[DefinitionName.ObjectPageFooter] as Definition).properties[PropertyName.actions] = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.FooterActions}`
    };
    const footerActions = (appSchema.definitions[DefinitionName.FooterActions] = {
        type: 'object',
        properties: {},
        description: 'Actions',
        additionalProperties: false
    });
    footerActions[SchemaTag.isViewNode] = true;

    const alias = findAlias(UIVOCABULARY, generateParameters.serviceAVT);
    const entityType = generateParameters.entitySet.entityType;
    const actionAnnotation = alias && entityType.annotations?.[alias]?.Identification;
    if (actionAnnotation) {
        const convertedIdentification = actionAnnotation.fullyQualifiedName.replace(
            `@${UIAnnotationTerms.Identification}`,
            `/@${UIAnnotationTerms.Identification}`
        );
        footerActions[SchemaTag.annotationPath] = `/${convertedIdentification}`;
        actionAnnotation.forEach((item, index) => {
            if (item['Determining'] && item.$Type === UIAnnotationTypes.DataFieldForAction) {
                const { actionName, actionId } = getActionNameAndId(item.Action, entityType);
                const description = item.Label || actionName;
                const actionDefinitionKey = prepareRef(`${DefinitionName.ObjectPageFooterAction}<${actionId}>`);
                if (appSchema.definitions[DefinitionName.ObjectPageFooterAction]) {
                    appSchema.definitions[actionDefinitionKey] = {
                        ...JSON.parse(JSON.stringify(appSchema.definitions[DefinitionName.ObjectPageFooterAction])),
                        description,
                        annotationPath: `/${convertedIdentification}/${index}`
                    };
                } else {
                    appSchema.definitions[actionDefinitionKey] = {
                        type: 'object',
                        properties: {},
                        description: description
                    };
                    appSchema.definitions[actionDefinitionKey][SchemaTag.isViewNode] = true;
                    appSchema.definitions[actionDefinitionKey][
                        SchemaTag.annotationPath
                    ] = `/${convertedIdentification}/${index}`;
                }
                footerActions.properties[actionId] = {
                    $ref: `${DEFINITION_LINK_PREFIX}${actionDefinitionKey}`
                };
            }
        });
    } else {
        footerActions[
            SchemaTag.annotationPath
        ] = `/${entityType.fullyQualifiedName}/@${UIAnnotationTerms.Identification}`;
    }
    delete appSchema.definitions[DefinitionName.ObjectPageFooterActions];
    delete appSchema.definitions[`${DefinitionName.ObjectPageFooter}<${DefinitionName.ObjectPageFooterActions}>`];
}

/**
 * Add Object Page Header Action Buttons to app-specific schema
 * @param {object} appSchema Schema of the app
 * @param {EntityType} entityType - current entity type
 * @param {ConvertedMetadata} oDataServiceAVT - complete service information, as returned by annotation vocabularies tool
 */
export function addHeaderActions(
    appSchema: Definition,
    entityType: EntityType,
    oDataServiceAVT: ConvertedMetadata,
    addHeaderActionCallBack
): void {
    (appSchema.definitions[DefinitionName.ObjectPageHeader] as Definition).properties[PropertyName.actions] = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.HeaderActions}`
    };
    const headerActions = (appSchema.definitions[DefinitionName.HeaderActions] = {
        type: 'object',
        properties: {},
        description: 'Actions',
        additionalProperties: false
    });
    headerActions[SchemaTag.isViewNode] = true;

    const alias = findAlias(UIVOCABULARY, oDataServiceAVT);
    const identification =
        alias &&
        entityType.annotations &&
        entityType.annotations[alias] &&
        entityType.annotations[alias].Identification;

    if (identification) {
        headerActions[SchemaTag.annotationPath] =
            '/' +
            identification.fullyQualifiedName.replace(
                `@${UIAnnotationTerms.Identification}`,
                `/@${UIAnnotationTerms.Identification}`
            );
        identification.forEach(
            addHeaderActionCallBack(entityType, appSchema, identification, headerActions, oDataServiceAVT)
        );
    } else {
        headerActions[
            SchemaTag.annotationPath
        ] = `/${entityType.fullyQualifiedName}/@${UIAnnotationTerms.Identification}`;
    }
    delete appSchema.definitions[DefinitionName.GenericActions];
    delete appSchema.definitions[`${DefinitionName.ObjectPageHeader}<${DefinitionName.GenericActions}>`];
}

/**
 * Adds common Object Page Header definitions to the app schema
 * @param appSchema - app-specific JSOn schema
 * @param entityType - current entity Type
 */
export function addCommonHeaderSchema(appSchema: Definition, entityType: EntityType): void {
    appSchema.properties['header'] = {
        $ref: `${DEFINITION_LINK_PREFIX}${DefinitionName.ObjectPageHeader}`
    };
    appSchema.definitions[DefinitionName.ObjectPageHeader] = JSON.parse(
        JSON.stringify(appSchema.definitions[`${DefinitionName.ObjectPageHeader}<${DefinitionName.GenericActions}>`])
    );
    appSchema.definitions[DefinitionName.ObjectPageHeader][
        SchemaTag.annotationPath
    ] = `/${entityType.fullyQualifiedName}/@${UIAnnotationTerms.HeaderInfo}`;
}

/**
 * Common logic to determine the EntitySet of a facet
 * @param facet - current facet or section
 * @param serviceAVT - AVT converter output, comprising all annotation information
 * @returns the entity set as defined in AVT, if found
 */
export function determineEntitySetOfFacet(facet: FacetConfig, serviceAVT: ConvertedMetadata): EntitySet | undefined {
    return facet.entityType
        ? serviceAVT.entitySets.find(
              (es) => es.entityType.name === facet.entityType.name || es.name === facet.entityType.name
          )
        : undefined;
}
