import type { v4, Manifest } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getColumnsPathForLR, getColumnsPathForView } from '../../application';

export class ActionAfterExecutionConfiguration implements v4.ActionAfterExecutionConfiguration {
    @syncRule({
        manifest: {
            path: (
                pageName: string[],
                manifest: Manifest,
                sectionId: string,
                columnKey: string,
                targetAnnotation: string
            ): string =>
                getColumnsPathForLR(pageName, manifest, sectionId, columnKey, targetAnnotation) + '/afterExecution'
        }
    })
    navigateToInstance?: boolean;

    @syncRule({
        manifest: {
            path: (
                pageName: string[],
                manifest: Manifest,
                sectionId: string,
                columnKey: string,
                targetAnnotation: string
            ): string =>
                getColumnsPathForLR(pageName, manifest, sectionId, columnKey, targetAnnotation) + '/afterExecution'
        }
    })
    enableAutoScroll?: boolean;
}

/************************************
 * Sync class for TableColumn
 */
export class TableColumn implements v4.TableColumn {
    @syncRule({
        manifest: {
            path: getColumnsPathForLR
        }
    })
    @validity({
        since: '1.84.0'
    })
    width?: string;

    @syncRule({
        manifest: {
            path: getColumnsPathForLR
        }
    })
    @validity({
        since: '1.85.0'
    })
    availability?: v4.Availability;
}

