/**
 * Sync class for Position
 */
export class Position implements v4.Position {
    @syncRule({
        manifest: {
            path: (
                pageName: string[],
                manifest: Manifest,
                sectionId: string,
                columnKey: string,
                targetAnnotation: string
            ): string => getColumnsPathForLR(pageName, manifest, sectionId, columnKey, targetAnnotation) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    anchor?: string;
    @syncRule({
        manifest: {
            path: (
                pageName: string[],
                manifest: Manifest,
                sectionId: string,
                columnKey: string,
                targetAnnotation: string
            ): string => getColumnsPathForLR(pageName, manifest, sectionId, columnKey, targetAnnotation) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    placement: v4.Placement;
}

