import type { Definition } from 'typescript-json-schema';
import type { MetadataInstanceInterface } from '../../common/decoration/factory';
import type { v2, PageType, PageTypeV4, FileData, SyncRule } from '@sap/ux-specification-types';
import { ArtifactType, DefinitionName, SchemaTag } from '@sap/ux-specification-types';
import type { AnnotationTerm, EntityType } from '@sap-ux/vocabularies-types';
import type { DataFieldAbstractTypes, LineItem, SelectionFields } from '@sap-ux/vocabularies-types/vocabularies/UI';
import {
    addKeyToDefinition,
    getActionKey,
    DEFINITION_LINK_PREFIX,
    addDataTypeToDefinition,
    addCommonLineItemDefinitions,
    createAnnotationPath,
    determineDataType,
    getDataFieldDescription,
    getLabelForPropertyPath,
    getNextTargetDefinition,
    getSchemaKeyOfLineItemRecord,
    prepareRef
} from '../../common';
import { getReflectMetadata } from '../../common/decoration/decorators';
import { UIAnnotationTypes, UIAnnotationTerms } from '@sap-ux/vocabularies-types/vocabularies/UI';
import { sep } from 'path';
import { xml2json } from 'xml-js';
import { ActionTypeV2 } from '../types';

/**
 * Common function for enhancing the LineItems definiton of app schema by custom columns
 * @param appSchema - app-specific JSON schema
 * @param customColumnsDefinitionName - custom column definition name (plural)
 * @param customColumnDefinitionName - custom column definition name (singular)
 */
function addCustomColumnDefinition(
    appSchema: Definition,
    customColumnsDefinitionName: string,
    customColumnDefinitionName: string
): void {
    if (appSchema.definitions[DefinitionName.LineItems]) {
        (appSchema.definitions[DefinitionName.LineItems] as Definition).properties['custom'] = {
            $ref: `${DEFINITION_LINK_PREFIX}${customColumnsDefinitionName}`
        };
    }
    appSchema.definitions[customColumnsDefinitionName] = {
        type: 'array',
        items: {
            $ref: `${DEFINITION_LINK_PREFIX}${customColumnDefinitionName}`
        },
        description: 'Custom Columns'
    };
    appSchema.definitions[customColumnsDefinitionName][SchemaTag.isViewNode] = true;
    const extensionType = (appSchema.definitions[customColumnDefinitionName] as Definition).properties['extensionType'];
    if (extensionType) {
        extensionType[SchemaTag.artifactType] = ArtifactType.Manifest;
    }
}

/**
 * Prepares and returns id of action for further use and reference
 * @param actionType - type of action
 * @param lineItemProperties - properties of the line item collection: lineItemPath, lineItemRecord, lineItemId
 * @returns {string} actionId
 */
function getActionId(
    actionType: ActionTypeV2,
    lineItemProperties: { lineItemPath: any; lineItemRecord: any; lineItemId: any; entityType: EntityType }
): string {
    let actionId: string;
    if (actionType === ActionTypeV2.ColumnIntentBased) {
        actionId = getSchemaKeyOfLineItemRecord(lineItemProperties.lineItemRecord);
    } else if (actionType === ActionTypeV2.ToolBarIntentBased || actionType === ActionTypeV2.FooterIntentBased) {
        actionId = prepareRef(
            `${lineItemProperties.lineItemRecord.SemanticObject}::${lineItemProperties.lineItemRecord.Action}`
        );
    } else if (
        actionType === ActionTypeV2.Column &&
        lineItemProperties.lineItemRecord.$Type === UIAnnotationTypes.DataFieldForAction
    ) {
        actionId = prepareRef(`DataFieldForAction:::sAction::${lineItemProperties.lineItemRecord.Action}`);
    } else {
        actionId = prepareRef(lineItemProperties.lineItemRecord.Action);
    }
    if (lineItemProperties.lineItemId && lineItemProperties.lineItemId !== 'LineItems') {
        return `${lineItemProperties.lineItemId}??${actionId}`;
    } else {
        return actionId;
    }
}

/**
 * Processes a LineItem record of type DataFieldForAction and DataFieldForIntentBasedNavigation during app schema generation
 * @param appSchema - the app specific schema that shall get enhanced
 * @param entityType - the entity type as part of the AVT ConvertedMetadata
 * @param actions - actions definition in schema, parent object
 * @param lineItemProperties - properties of the line item collection: lineItemPath, lineItemRecord, lineItemId
 * @param actionType - type of action
 * @param recordIndex - record index in the collection
 */
export function handleActionRecord(
    appSchema: Definition,
    actions: Definition,
    lineItemProps: { lineItemPath; lineItemRecord; lineItemId; entityType: EntityType },
    actionType: ActionTypeV2,
    recordIndex: number
): void {
    let actionReferenceType;
    const actionTemplate = lineItemProps.lineItemId === DefinitionName.LineItems ? '' : 'ObjectPage';
    const actionId = getActionId(actionType, lineItemProps);

    if (actionType === ActionTypeV2.Footer || actionType === ActionTypeV2.FooterIntentBased) {
        actionReferenceType = `Action`;
        actionType = ActionTypeV2.Footer;
    } else if (actionType === ActionTypeV2.Column || actionType === ActionTypeV2.ColumnIntentBased) {
        actionReferenceType = `TableColumnAction`;
    } else {
        actionReferenceType = actionTemplate !== '' ? `${actionTemplate}${actionType}` : `Action`;
        actionType = ActionTypeV2.ToolBar;
    }
    const actionDefinition = `${actionTemplate}${actionType}<${actionId}>`;
    const description = getDataFieldDescription(
        lineItemProps.lineItemRecord as DataFieldAbstractTypes,
        lineItemProps.entityType
    );
    actions.properties[actionId] = {
        $ref: DEFINITION_LINK_PREFIX + actionDefinition
    };
    actions.properties[actionId][SchemaTag.propertyIndex] = recordIndex;
    const { key, ns } = getActionKey(lineItemProps.lineItemRecord.Action, true);
    if (appSchema.definitions[actionReferenceType]) {
        appSchema.definitions[actionDefinition] = {
            ...JSON.parse(JSON.stringify(appSchema.definitions[actionReferenceType])),
            description,
            key,
            annotationPath: `${lineItemProps.lineItemPath}/${recordIndex}`
        };
        if (ns) {
            appSchema.definitions[actionDefinition][SchemaTag.target] = ns;
        }
    } else {
        appSchema.definitions[actionDefinition] = {
            type: 'object',
            properties: {},
            description,
            additionalProperties: false
        };
        appSchema.definitions[actionDefinition][SchemaTag.isViewNode] = true;
        appSchema.definitions[actionDefinition][SchemaTag.key] = `Action: ${key}`;
        if (ns) {
            appSchema.definitions[actionDefinition][SchemaTag.target] = ns;
        }
        appSchema.definitions[actionDefinition][
            SchemaTag.annotationPath
        ] = `${lineItemProps.lineItemPath}/${recordIndex}`;
    }
}

/**
 * Returns the type of a column lineItem action
 * @param lineItemId - optional: in case of OP the parameter must be passed to distinguish the OP tables;
 * @param {object} lineItemRecord - actual record of the line item collection
 * @param actionTypes - array of action types to be filled
 */
function getColumnActionType(lineItemId: string, lineItemRecord, actionTypes: ActionTypeV2[]) {
    if (
        lineItemId !== DefinitionName.LineItems &&
        lineItemRecord.Inline === true &&
        (lineItemRecord.$Type === UIAnnotationTypes.DataFieldForAction ||
            lineItemRecord.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation)
    ) {
        actionTypes.push(ActionTypeV2.OPColumn);
    } else if (lineItemRecord.Inline === true && lineItemRecord.$Type === UIAnnotationTypes.DataFieldForAction) {
        actionTypes.push(ActionTypeV2.Column);
    } else if (
        lineItemRecord.Inline === true &&
        lineItemRecord.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation
    ) {
        actionTypes.push(ActionTypeV2.ColumnIntentBased);
    }
}

/**
 * Returns the type of a lineItem action
 * @param {object} lineItemRecord - actual record of the line item collection
 * @returns {string[] | undefined } ActionTypeV2[] - action type(s) of the lineItem
 */
function getActionType(lineItemRecord: DataFieldAbstractTypes, lineItemId: string): ActionTypeV2[] | undefined {
    const actionTypes: ActionTypeV2[] = [];
    if (
        lineItemRecord.$Type !== UIAnnotationTypes.DataFieldForAction &&
        lineItemRecord.$Type !== UIAnnotationTypes.DataFieldForIntentBasedNavigation
    ) {
        return undefined;
    }
    if (
        lineItemRecord.Inline !== true &&
        lineItemRecord.Determining !== true &&
        lineItemRecord.$Type === UIAnnotationTypes.DataFieldForAction
    ) {
        actionTypes.push(ActionTypeV2.ToolBar);
    } else if (
        lineItemRecord.Inline !== true &&
        lineItemRecord.Determining !== true &&
        lineItemRecord.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation
    ) {
        actionTypes.push(ActionTypeV2.ToolBarIntentBased);
    } else if (lineItemRecord.Determining === true && lineItemRecord.$Type === UIAnnotationTypes.DataFieldForAction) {
        actionTypes.push(ActionTypeV2.Footer);
    } else if (
        lineItemRecord.Determining === true &&
        lineItemRecord.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation
    ) {
        actionTypes.push(ActionTypeV2.FooterIntentBased);
    }
    getColumnActionType(lineItemId, lineItemRecord, actionTypes);
    return actionTypes;
}

type LineItemProperties = {
    lineItemPath: string;
    lineItemRecord: DataFieldAbstractTypes;
    lineItemId: string;
    entityType: EntityType;
};

function generateGenericColumn(lineItemProperties: LineItemProperties, appSchema: Definition, recordIndex: number) {
    const schemaKey = getSchemaKeyOfLineItemRecord(lineItemProperties.lineItemRecord);
    if (schemaKey) {
        const lineItemDefinition = appSchema.definitions[lineItemProperties.lineItemId] as Definition;
        lineItemDefinition.properties[schemaKey] = {
            $ref: DEFINITION_LINK_PREFIX + DefinitionName.TableColumn,
            description: getDataFieldDescription(lineItemProperties.lineItemRecord, lineItemProperties.entityType)
            // Custom property in schema - for object properties ordering purpose
        };
        lineItemDefinition.properties[schemaKey][SchemaTag.propertyIndex] = recordIndex;
        lineItemDefinition.properties[schemaKey][
            SchemaTag.annotationPath
        ] = `${lineItemProperties.lineItemPath}/${recordIndex}`;
        addDataTypeToDefinition(
            lineItemDefinition.properties[schemaKey] as Definition,
            lineItemProperties.lineItemRecord
        );
        addKeyToDefinition(lineItemProperties.lineItemRecord, lineItemDefinition.properties, schemaKey);
    }
}

function handleAnyAction(
    actionType: ActionTypeV2,
    appSchema: Definition,
    actions: object,
    lineItemProperties: LineItemProperties,
    recordIndex: number,
    footerActions: object,
    lineItemId: string
) {
    switch (actionType) {
        case ActionTypeV2.ToolBar:
        case ActionTypeV2.ToolBarIntentBased: {
            handleActionRecord(appSchema, actions, lineItemProperties, actionType, recordIndex);
            break;
        }
        case ActionTypeV2.Footer:
        case ActionTypeV2.FooterIntentBased: {
            if (footerActions) {
                handleActionRecord(appSchema, footerActions, lineItemProperties, actionType, recordIndex);
            }
            break;
        }
        case ActionTypeV2.Column:
        case ActionTypeV2.ColumnIntentBased: {
            handleActionRecord(
                appSchema,
                appSchema.definitions[lineItemId] as Definition,
                lineItemProperties,
                actionType,
                recordIndex
            );
            break;
        }
        case ActionTypeV2.OPColumn: {
            generateGenericColumn(lineItemProperties, appSchema, recordIndex);
        }
    }
}

/**
 * Adds the line item records to the app schema (for the list report or an object page section comprising a table)
 * @param appSchema - the app specific schema that shall get enhanced
 * @param lineItemAnnotation - the UI.LineItem annotation, comprising all records
 * @param entityType - the entity type as part of the AVT ConvertedMetadata
 * @param lineItemId - optional: in case of OP the parameter must be passed to distinguish the OP tables;
 *                      in case of LR or ALP no ID is passed, 'LineItems' is taken then.
 * @param customColumnRef - schema reference to custom column definition.
 */
export function addLineItemsType(
    appSchema: Definition,
    lineItemAnnotation: AnnotationTerm<LineItem>,
    entityType: EntityType,
    lineItemId?: string,
    customColumnRef?: string
): void {
    // Add the correct lineItem definition
    lineItemId = lineItemId || DefinitionName.LineItems;

    const { actions, footerActions, lineItemPath } = addCommonLineItemDefinitions(
        appSchema,
        lineItemAnnotation,
        entityType,
        lineItemId
    );

    if (lineItemAnnotation) {
        let recordIndex = 0;
        lineItemAnnotation.forEach((lineItemRecord) => {
            const lineItemProperties: LineItemProperties = { lineItemPath, lineItemRecord, lineItemId, entityType };
            const actionTypes = getActionType(lineItemRecord, lineItemId);
            if (actionTypes) {
                actionTypes.forEach((actionType) => {
                    handleAnyAction(
                        actionType,
                        appSchema,
                        actions,
                        lineItemProperties,
                        recordIndex,
                        footerActions,
                        lineItemId
                    );
                });
            } else {
                generateGenericColumn(lineItemProperties, appSchema, recordIndex);
            }
            recordIndex++;
        });
    }
    if (customColumnRef) {
        (appSchema.definitions[lineItemId] as Definition).properties['custom'] = {
            $ref: `${DEFINITION_LINK_PREFIX}${customColumnRef}`
        };
    }
}

/**
 * Adds enum entries of possible leading properties for 'leadingProperty' property in TableCustomColumn properties.
 * @param appSchema - the app specific schema that shall get enhanced
 * @param entityType - the entity type as part of the AVT ConvertedMetadata
 * @param definitionName - definition name of CustomColumn definition
 */
export function addListReportLeadingProperties(
    appSchema: object,
    entityType: EntityType,
    definitionName: string
): void {
    // Leading Property
    const leadingPropertySchema =
        appSchema['definitions'][definitionName] &&
        appSchema['definitions'][definitionName]['properties'] &&
        appSchema['definitions'][definitionName]['properties']['leadingProperty'];
    if (leadingPropertySchema) {
        const properties = [
            //  Entity properties
            ...(entityType?.entityProperties || []),
            // Navigation Properties
            ...(entityType?.navigationProperties || [])
        ];
        leadingPropertySchema['enum'] = properties?.reduce((result: string[], property) => {
            if (!result.includes(property.name)) {
                result.push(property.name);
            }
            return result;
        }, []);
    }
}

/**
 * Adds enum entries of posible tabKey properties for 'leadingProperty' property in TableCustomColumn properties.
 * @param appSchema - the app specific schema that shall get enhanced
 * @param definitionName - definition name of CustomColumn definition
 * @param page - sapui5 page object
 */
export function addTabkeyEnum(appSchema: object, definitionName: string, page?: v2.SapUiAppPageV2): void {
    const tabkeyPropertySchema =
        appSchema['definitions'][definitionName] &&
        appSchema['definitions'][definitionName]['properties'] &&
        appSchema['definitions'][definitionName]['properties']['tabKey'];
    if (page) {
        const quickVariantsX =
            page.component &&
            page.component.settings &&
            page.component.settings.quickVariantSelectionX &&
            page.component.settings.quickVariantSelectionX['variants'];
        if (quickVariantsX) {
            const enumEntries = [];
            Object.keys(quickVariantsX).reduce((result: string[], variant) => {
                const key =
                    quickVariantsX[variant] && quickVariantsX[variant]['key'] !== undefined
                        ? quickVariantsX[variant]['key']
                        : undefined;
                // Avoid duplicates
                if (key !== undefined && !result.includes(key)) {
                    result.push(key);
                }
                return result;
            }, enumEntries);
            tabkeyPropertySchema['enum'] = enumEntries;
        }
    }
}

/**
 * Adds enum entries of posible fragment entries.
 * @param {object} appSchema The app specific schema that shall get enhanced.
 * @param {FileData[]} fragments Array of fragments.
 * @param {string[]} appId Application id.
 * @param {string} property Property name.
 * @param {string} definitionName Definition name of CustomColumn definition.
 */
export function addFragmentEnum(
    appSchema: Definition,
    fragments: FileData[],
    appId: string,
    property: string,
    definitionName: string
): void {
    if (!fragments || !appId || appId.startsWith('${')) {
        return;
    }
    const enumEntries = [];
    fragments.forEach((fragment) => {
        let relPath = fragment.dataSourceUri.split('webapp')[1];
        if (relPath && relPath.indexOf('.fragment.xml') > 0) {
            const fragmentJson = JSON.parse(xml2json(fragment.fileContent, { compact: true }))[
                'core:FragmentDefinition'
            ];
            if (
                (property === 'fragmentName' &&
                    (!fragmentJson || fragmentJson.Column || fragmentJson['table:Column'])) ||
                (property === 'cellsFragmentName' &&
                    (!fragmentJson ||
                        (!fragmentJson.Column &&
                            !fragmentJson['table:Column'] &&
                            !fragmentJson['smartfilterbar:ControlConfiguration'])))
            ) {
                relPath = relPath.replace(new RegExp('\\' + sep, 'g'), '/');
                const fragmentName = appId + relPath.split('.fragment.xml')[0].replace(/\//g, '.');
                enumEntries.push(fragmentName);
            }
        }
    });
    if (enumEntries.length > 0) {
        const fragmentPropertySchema = appSchema.definitions[definitionName]?.['properties']?.[property];
        fragmentPropertySchema['enum'] = enumEntries;
    }
}

/**
 * Adds the custom columns to the line item records of the app schema (for the list report comprising a table).
 * @param {object} appSchema The app specific schema that shall get enhanced.
 * @param {EntityType} entityType The entity type as part of the AVT ConvertedMetadata.
 * @param {SapUiAppPageV2} page Sapui5 page object.
 * @param {object} manifest Application manifest.
 * @param {FileData[]} [fragments] Array of fragments.
 * @param {string} [customColumnsDefinitionName=DefinitionName.CustomColumns] Definition name of Custom Columns definition.
 * @param {string} [customColumnDefinitionName=DefinitionName.CustomColumn] Definition name of Custom Column definition.
 */
export function addTableColumnExtensions(
    appSchema: object,
    entityType: EntityType,
    page: v2.SapUiAppPageV2,
    manifest: object,
    fragments?: FileData[],
    customColumnsDefinitionName: string = DefinitionName.CustomColumns,
    customColumnDefinitionName: string = DefinitionName.CustomColumn
): void {
    addCustomColumnDefinition(appSchema, customColumnsDefinitionName, customColumnDefinitionName);

    // Leading Property
    addListReportLeadingProperties(appSchema, entityType, customColumnDefinitionName);
    // Tab key property
    addTabkeyEnum(appSchema, customColumnDefinitionName, page);
    // Fragment property
    const appId = manifest && manifest['sap.app'] && manifest['sap.app']['id'];
    addFragmentEnum(appSchema, fragments, appId, 'fragmentName', customColumnDefinitionName);
    addFragmentEnum(appSchema, fragments, appId, 'cellsFragmentName', customColumnDefinitionName);
}

/**
 * Processes an anyOf definition of the generic schema
 * @param propertyDefinition - current definition in the generic schema
 * @param schema - the app-specific schema
 * @param propertyKey - key of the single property
 * @param factory - factory for creating metadata instances
 * @param pageType - page type
 */
function processAnyOf(
    propertyDefinition: any,
    schema: object,
    propertyKey: string,
    factory: MetadataInstanceInterface,
    pageType: PageType | PageTypeV4
) {
    propertyDefinition['anyOf'].forEach((element) => {
        const nextDefinition = getNextTargetDefinition(schema, '', {}, element, propertyKey, factory, pageType);
        if (nextDefinition) {
            // eslint-disable-next-line @typescript-eslint/no-use-before-define
            processMetadata(schema, nextDefinition.targetDefinition, nextDefinition.configObject, pageType, factory);
        }
    });
}

/**
 * Processes an object reference of the generic schema
 * @param schema
 * @param propertyDefinition - current definition in the generic schema
 * @param propertyKey - key of the single property
 * @param factory - factory for creating metadata instances
 * @param pageType - page type
 */
function processObject(
    schema: object,
    propertyDefinition: any,
    propertyKey: string,
    factory: MetadataInstanceInterface,
    pageType: PageType | PageTypeV4
) {
    const nextDefinition = getNextTargetDefinition(schema, '', {}, propertyDefinition, propertyKey, factory, pageType);
    if (nextDefinition) {
        // eslint-disable-next-line @typescript-eslint/no-use-before-define
        processMetadata(schema, nextDefinition.targetDefinition, nextDefinition.configObject, pageType, factory);
    }
}

/**
 * Determines the artifact type of a property and adds it to the property definition in schema
 * @param syncRule - sync rule of the current property
 * @param propertyDefinition - current definition in the generic schema
 */
function addArtifactType(syncRule: SyncRule, propertyDefinition: any) {
    if (syncRule.manifest !== undefined) {
        propertyDefinition.artifactType = ArtifactType.Manifest;
    } else if (syncRule.flex !== undefined) {
        propertyDefinition.artifactType = ArtifactType.FlexChange;
        //Add control type
        if (syncRule.flex.controlType) {
            propertyDefinition.controlType = syncRule.flex.controlType();
        }
    } else {
        propertyDefinition.artifactType = ArtifactType.Annotation;
    }
}

/**
 * After the generation of a generic schema, apply post-generation rule to a single property
 * @param metaDataDefinition
 * @param schema - given app schema, to be adapted
 * @param pageType - page type
 * @param factory - factory for creating metadata instances
 * @param propertyDefinition - definition of the single property
 * @param propertyKey - key of the single property
 * @param schemaDefinition - the current entry point or definition in the app schema
 */
function processSingleProperty(
    metaDataDefinition: { [key: string]: any },
    schema: object,
    pageType: PageType | PageTypeV4,
    factory: MetadataInstanceInterface,
    propertyDefinition: any,
    propertyKey: string,
    schemaDefinition: object
): void {
    if (metaDataDefinition) {
        const syncRule = getReflectMetadata(metaDataDefinition, propertyKey);
        if (syncRule) {
            if (typeof syncRule.generate === 'function') {
                syncRule.generate(schema, schemaDefinition, propertyKey);
                propertyDefinition = schemaDefinition['properties'][propertyKey];
            }
            //Add artifact type
            addArtifactType(syncRule, propertyDefinition);
        }
        if (propertyDefinition.type === 'object' || propertyDefinition.$ref) {
            processObject(schema, propertyDefinition, propertyKey, factory, pageType);
        } else if (propertyDefinition['anyOf']) {
            processAnyOf(propertyDefinition, schema, propertyKey, factory, pageType);
        }
    }
}

/**
 * After the generation of a generic schema, apply rules to a list of properties of the given schema section
 * @param schema - given app schema, to be adapted
 * @param schemaDefinition - the current entry point or definition in the app schema
 * @param pageType - page type
 * @param factory - factory for creating metadata instances
 */
function processProperties(
    schema: object,
    schemaDefinition: object,
    metaDataDefinition: { [key: string]: any },
    propertiesSection: string,
    pageType: PageType | PageTypeV4,
    factory: MetadataInstanceInterface
) {
    if (schemaDefinition[propertiesSection]) {
        for (const propertyKey in schemaDefinition[propertiesSection]) {
            if (propertyKey === 'anyOf') {
                processAnyOf(schemaDefinition[propertiesSection], schema, propertyKey, factory, pageType);
            } else {
                const propertyDefinition = schemaDefinition[propertiesSection][propertyKey];
                if (propertyDefinition['anyOf']) {
                    processAnyOf(propertyDefinition, schema, propertyKey, factory, pageType);
                } else if (propertyKey === '$ref') {
                    processObject(schema, schemaDefinition[propertiesSection], propertyKey, factory, pageType);
                } else {
                    processSingleProperty(
                        metaDataDefinition,
                        schema,
                        pageType,
                        factory,
                        propertyDefinition,
                        propertyKey,
                        schemaDefinition
                    );
                }
            }
        }
    }
}

/**
 * Apply post-processing rules as specified in form of "generate" at the syncRule
 * @param schema - given app schema, to be adapted
 * @param schemaDefinition - the current entry point or definition in the app schema
 * @param pageType - page type
 * @param factory - factory for creating metadata instances
 */
export function processMetadata(
    schema: object,
    schemaDefinition: object,
    metaDataDefinition: { [key: string]: any },
    pageType: PageType | PageTypeV4,
    factory: MetadataInstanceInterface
): void {
    processProperties(schema, schemaDefinition, metaDataDefinition, 'properties', pageType, factory);
    processProperties(schema, schemaDefinition, metaDataDefinition, 'additionalProperties', pageType, factory);
    processProperties(schema, schemaDefinition, metaDataDefinition, 'patternProperties', pageType, factory);
}

/**
 * Method updates passed 'enum' array by removing passed matching entries.
 * @param {Array<unknown>} values Array of values.
 * @param {Array<unknown>} valuesToRemove Array of values to remove.
 */
export function removeElementsFromEnum(values: Array<unknown>, valuesToRemove: Array<unknown>): void {
    for (const value of valuesToRemove) {
        const index = values.indexOf(value);
        if (index !== -1) {
            // Value exists - remove it
            values.splice(index, 1);
        }
    }
}

/**
 * Adds the selection fields to the app schema, as properties of the Filterbar
 * @param entityType - the entity type as part of the AVT ConvertedMetadata
 * @param appSchema - the app specific schema that shall get enhanced
 * @param selectionFieldsAnnotation - the UI.SelectionField annotation
 */
export function addSelectionFields(
    appSchema: object,
    selectionFieldsAnnotation: AnnotationTerm<SelectionFields>,
    entityType: EntityType
): void {
    const selectionFieldsDefinition = (appSchema['definitions'].SelectionFields = {
        ...appSchema['definitions'].SelectionFields,
        properties: {},
        additionalProperties: false,
        annotationPath: createAnnotationPath(entityType.fullyQualifiedName, UIAnnotationTerms.SelectionFields)
    });
    if (!selectionFieldsAnnotation) {
        return;
    }
    let i = 0;
    selectionFieldsAnnotation.forEach((selectionField) => {
        let description = selectionField.value;
        const fieldDefinition = `SelectionField<${prepareRef(selectionField.value)}>`;
        const dataType = determineDataType(selectionField);
        if (selectionField.type === 'PropertyPath') {
            description = getLabelForPropertyPath(selectionField, entityType);
        }
        selectionFieldsDefinition['properties'][selectionField.value] = {
            $ref: DEFINITION_LINK_PREFIX + fieldDefinition,
            propertyIndex: i
        };
        appSchema['definitions'][fieldDefinition] = {
            type: 'object',
            description,
            key: `Value: ${selectionField.value}`,
            annotationPath: `${selectionFieldsDefinition.annotationPath}/${i}`,
            isViewNode: true,
            additionalProperties: false,
            annotationType: selectionField?.type,
            dataType
        };
        if (['Date', 'DateTime', 'DateTimeOffset'].includes(dataType)) {
            appSchema['definitions'][fieldDefinition]['$ref'] = `${DEFINITION_LINK_PREFIX}SelectionFieldDateSettings`;
        } else {
            appSchema['definitions'][fieldDefinition].properties = {};
        }
        i++;
    });
}

/**
 * Add entityProperties to passed a passed enum entry
 * @param {string[]} appSchemaEnum - Enum to be enhanced
 * @param {EntityType} entityType - Given entity type for which entries shall get generated
 */
export function addEntityPropertiesToEnum(appSchemaEnum: string[], entityType: EntityType): void {
    for (const key of entityType.entityProperties) {
        appSchemaEnum.push(key.name);
    }
}
