import type { v2, ControlTypeFunction } from '@sap/ux-specification-types';
import { ControlType, DefinitionName } from '@sap/ux-specification-types';
import { syncRule } from '../../../common/decoration/decorators';
import {
    addPatternForBindingChangeOfEnumOP,
    buildControlIdFromParent,
    exportToFlexChange,
    convertSectionId
} from '../../../common';

export const getTableSectionControlId = (
    baseId: string,
    _idBreadcrumbs: string[],
    breadcrumbs: string[],
    _controlType: ControlTypeFunction,
    facetId: string
): string => {
    const convertedId = convertSectionId(breadcrumbs[1], facetId);
    const breadcrumbsTrail = [convertedId, DefinitionName.Table];
    return buildControlIdFromParent(baseId, breadcrumbsTrail);
};
