export class ObjectPageSectionV2 implements v2.ObjectPageSectionV2 {
    @syncRule({
        flex: {
            controlId: getSectionControlId,
            controlType: (): ControlType => ControlType.Section,
            createFlexChange: exportToFlexChange
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    visible?: boolean;
}

