import type { v4, Manifest } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getColumnsPathForOP } from '../../application';

export class ActionAfterExecutionConfigurationOP implements v4.ActionAfterExecutionConfigurationOP {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getColumnsPathForOP(pageName, manifest, sectionId, columnKey) + '/afterExecution'
        }
    })
    navigateToInstance?: boolean;

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getColumnsPathForOP(pageName, manifest, sectionId, columnKey) + '/afterExecution'
        }
    })
    enableAutoScroll?: boolean;
}

export class ObjectPageTableColumn implements v4.ObjectPageTableColumn {
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    width?: string;

    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.85.0'
    })
    availability?: v4.Availability;
}

export class ObjectPageTableColumnAction extends ObjectPageTableColumn implements v4.ObjectPageTableColumnAction {
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    afterExecution?: ActionAfterExecutionConfigurationOP;
}
export class PositionOP implements v4.PositionOP {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getColumnsPathForOP(pageName, manifest, sectionId, columnKey) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    anchor?: string;
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string, columnKey): string =>
                getColumnsPathForOP(pageName, manifest, sectionId, columnKey) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    placement: v4.Placement;
}
export class TableCustomColumnOP implements v4.TableCustomColumnOP {
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    position?: PositionOP;
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    header: string;
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    width?: string;
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    template: string;
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.86.0'
    })
    horizontalAlign?: v4.HorizontalAlign;
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    availability?: v4.Availability;
    @syncRule({
        manifest: {
            path: getColumnsPathForOP
        }
    })
    @validity({
        since: '1.85.0'
    })
    properties?: v4.ColumnPropertiesType;
}
