import type { v2 } from '@sap/ux-specification-types';
import { syncRule } from '../../../common/decoration/decorators';
import { getCardSettingsPath } from '../../application';

export class CommonCardSettings implements v2.CommonCardSettings {
    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    entitySet?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    title: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    subTitle?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    identificationAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    selectionAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    presentationAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    defaultSpan?: v2.DefaultSpanDef;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    requireAppAuthorization?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    tabs?: [v2.TabSetting];

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    addODataSelect?: boolean;
}

export class CustomCard extends CommonCardSettings implements v2.CustomCard, v2.CustomCardSettings {
    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    model: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    template: v2.CardTemplateType;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    settings?: v2.CustomCardSettings;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    itemText?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    valueSelectionInfo?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    staticContent?: {
        [k: string]: any;
    }[];

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    listFlavor?: 'standard' | 'bar' | 'carousel';

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    listType?: 'extended' | 'condensed';

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    sortBy?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    sortOrder?: 'ascending' | 'descending';

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    annotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    chartAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    dataPointAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    kpiAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    selectionPresentationAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    dynamicSubtitleAnnotationPath?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    ignoreSapText?: boolean;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    objectStreamCardsSettings?: {
        showFirstActionInFooter?: boolean;
        annotationPath?: string;
    };

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    enableLocaleCurrencyFormatting?: boolean;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    navigation?: 'dataPointNav' | 'chartNav' | 'headerNav' | 'noHeaderNav';

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    showFilterInHeader?: boolean;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    showSortingInHeader?: boolean;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    imageSupported?: boolean;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    showLineItemDetail?: boolean;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    showLabelText?: boolean;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    customParams?: string;

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    chartProperties?: {
        plotArea?: v2.PlotAreaDef;
    };

    @syncRule({
        manifest: {
            path: (cardKeys): string => getCardSettingsPath(cardKeys)
        }
    })
    colorPalette?:
        | {
              [k: string]: any;
          }[]
        | {
              [k: string]: any;
          };
    [k: string]: any;
}

