import type { v2 } from '@sap/ux-specification-types';
import { ControlType, DefinitionName, VOCWITHCOLONS } from '@sap/ux-specification-types';
import type { Definition } from 'typescript-json-schema';

export type FlexForTransfer = {
    controlId: string;
    controlType: string;
    propertyId: string;
    facetId?: string;
    newValue: any;
    creation: string;
};

export interface FlexChangesForTransfer {
    [controlId: string]: FlexForTransfer;
}

export type ConfigTableType = v2.ResponsiveTable | v2.TreeTable | v2.AnalyticalTable | v2.GridTable | v2.TableALP;

export function addFlexForTable(config: ConfigTableType, change: FlexForTransfer, jsonSchema: Definition): void {
    if (change.controlType.endsWith(DefinitionName.Table)) {
        config[change.propertyId] = change.newValue;
    } else if (change.controlType === ControlType.TableColumn) {
        const schemaProperty = (jsonSchema?.definitions[DefinitionName.TableColumn] as Definition).properties[
            change.propertyId
        ];
        if (!schemaProperty) {
            return;
        }
        const idParts = change.controlId.split('-');
        const lastIdPart = idParts[idParts.length - 1];
        // As the last stable Id part is still too long in some cases, strip off the fixed parts.
        // It must the be added again by the export!
        const annotationPart = lastIdPart.split('template:::TableColumn:::');
        const colId = annotationPart[annotationPart.length - 1]
            .replace(/sSmartTableId.+?:::/, '')
            .replace('::com.sap.vocabularies', VOCWITHCOLONS)
            .replace('/', ':2f');
        if (!config.columns) {
            config.columns = {};
        }
        if (!config.columns[colId]) {
            config.columns[colId] = {};
        }
        config.columns[colId][change.propertyId] = change.newValue;
    }
}
