import { getGenericSchema } from '../../specification/schemaAccess';

interface UiEntityTypes {
    Facets: string[];
    LineItems: string[];
}

interface Schemas {
    [type: string]: object;
}

/**
 * Generates an object comprising all generic schemas
 * @param {TemplateType} templateType - Template type
 * @param {FioriElementsVersion} fioriElementsVersion - Fiori Elements version
 * @returns {Object} genericSchemas
 */
async function getGenericSchemas(
    templateType: TemplateType,
    fioriElementsVersion: FioriElementsVersion
): Promise<Schemas> {
    const genericSchemas: Schemas = {};
    let schemaTypes = [];
    schemaTypes = [SchemaType.Application];
    switch (templateType) {
        case TemplateType.ListReportObjectPageV2:
        case TemplateType.ListReportObjectPageV4:
        case TemplateType.AnalyticalListPageV4:
            schemaTypes.push(SchemaType.ListReport, SchemaType.ObjectPage);
            break;
        case TemplateType.AnalyticalListPageV2:
            schemaTypes.push(SchemaType.AnalyticalListPage, SchemaType.ObjectPage);
            break;
        case TemplateType.OverviewPageV2:
            schemaTypes.push(SchemaType.OverviewPage);
            break;
        case TemplateType.FreestylePageV4:
            schemaTypes.push(SchemaType.FreestylePage, SchemaType.ObjectPage);
            break;
        default:
            break;
    }
    for (const schemaType of schemaTypes) {
        const schemaFile = getSchemaFilePath(schemaType);
        genericSchemas[schemaFile.filename] = await getGenericSchema(fioriElementsVersion, schemaType);
    }
    return genericSchemas;
}

/**
 * Get the relevant UI.ListItem and UI.Facet entity sets
 * @param {ConvertedMetadata} oDataServiceAVT - Annotations as converted by AVT
 * @returns a fitered list of facet and line item annotations
 */
function getAnnotationsForUi(oDataServiceAVT: ConvertedMetadata): UiEntityTypes {
    const filteredEntityTypes: UiEntityTypes = {
        Facets: [],
        LineItems: []
    };
    const allEntityTypes = oDataServiceAVT?.entityTypes;
    if (!allEntityTypes) {
        return filteredEntityTypes;
    }
    const alias = findAlias(UIVOCABULARY, oDataServiceAVT);
    for (const entity of allEntityTypes) {
        if (!entity.annotations || !entity.annotations[alias]) {
            continue;
        }
        const { LineItem, Facets } = entity.annotations[alias];
        if (
            LineItem &&
            !LineItem.qualifier &&
            LineItem.find(
                (li) =>
                    li.$Type === UIAnnotationTypes.DataField || li.$Type === UIAnnotationTypes.DataFieldForAnnotation
            )
        ) {
            filteredEntityTypes.LineItems.push(entity.name);
        }
        if (Facets && !Facets.qualifier) {
            filteredEntityTypes.Facets.push(entity.name);
        }
    }
    return filteredEntityTypes;
}

export function getEntitySetByEntitySetName(entitySetName: string, oDataServiceAVT: ConvertedMetadata): EntitySet {
    return oDataServiceAVT?.entitySets && oDataServiceAVT.entitySets.find((es) => es.name === entitySetName);
}

/**
 * The function generates one app schema per entitySet that is linked with an object page in V2
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {Schema} genericSchemas - The generic schemas
 * @param {Schema} appSchemas - the list of app schemas that shall get extended
 */
function generateObjectPageSchemasV2(
    generateParameters: GenerateAppSchemaParameters,
    genericSchemas: Schemas,
    appSchemas: Schemas
): void {
    const allPages = getPagesV2(
        generateParameters.manifest[ManifestSection.generic].pages,
        v2.FE_TEMPLATE_V2_OBJECT_PAGE
    );
    for (const page in allPages) {
        const entitySetName = allPages[page].entitySet;
        const entitySet = getEntitySetByEntitySetName(entitySetName, generateParameters.serviceAVT);
        const entityTypeName = entitySet?.entityType?.name;
        const schemaFile = getSchemaFilePath(SchemaType.ObjectPage, entitySet?.name || entitySetName);
        const genericSchemaFile = getSchemaFilePath(SchemaType.ObjectPage);
        appSchemas[schemaFile.filename] = generateObjectPageSchemaV2(
            generateParameters,
            genericSchemas[genericSchemaFile.filename],
            entityTypeName
        );
    }
}

/**
 * The function generates one app schema per entitySet that is linked with an object page in V4
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {Schemas} genericSchemas - The generic schemas
 * @param {Schemas} appSchemas - the list of app schemas that shall get extended
 */
function generateObjectPageSchemasV4(
    generateParameters: GenerateAppSchemaParameters,
    genericSchemas: Schemas,
    appSchemas: Schemas
): void {
    const pages = getPagesV4(generateParameters.manifest, v4.FE_TEMPLATE_V4_OBJECT_PAGE);
    for (const page in pages) {
        const entitySetName = pages[page].options.settings.entitySet;
        const entitySet = getEntitySetByEntitySetName(entitySetName, generateParameters.serviceAVT);
        if (!entitySet) {
            log(generateParameters.logger, {
                severity: LogSeverity.Error,
                message: i18next.t('ENTITYSETNOTFOUND', { entitySet: entitySetName }),
                location: {
                    path: METADATAPATH
                }
            });
        }
        const entityTypeName = entitySet?.entityType?.name;
        const schemaFile = getSchemaFilePath(SchemaType.ObjectPage, entitySet?.name || entitySetName);
        const genericSchemaFile = getSchemaFilePath(SchemaType.ObjectPage);
        appSchemas[schemaFile.filename] = generateObjectPageSchemaV4(
            generateParameters,
            genericSchemas[genericSchemaFile.filename],
            entityTypeName
        );
    }
}

/**
 * Generates all app specific schemas for a Fiori Elements V2 application
 * @param {GenerateSchemaParameters} generateParameters - list of API input parameters
 * @param {Schemas} genericSchemas - The generic schemas
 * @param {UiEntityTypes} uiEntityTypes - list of all entity types, as comprised in oDataServiceAVT
 * @param {TemplateType} templateType - the given Fiori Elements' template type
 */
function generateAppSchemasV2(
    generateParameters: GenerateAppSchemaParameters,
    genericSchemas: Schemas,
    uiEntityTypes: UiEntityTypes,
    templateType: TemplateType
): Schemas {
    const appSchemas: Schemas = {};
    if (templateType === TemplateType.ListReportObjectPageV2 && uiEntityTypes) {
        /*--- List Report ----*/
        const allPages = getPagesV2(
            generateParameters.manifest[ManifestSection.generic].pages,
            v2.FE_TEMPLATE_V2_LIST_REPORT
        );

        for (const page in allPages) {
            const entitySetName = allPages[page].entitySet;
            const entitySet = getEntitySetByEntitySetName(entitySetName, generateParameters.serviceAVT);
            const entityTypeName = entitySet?.entityType?.name;
            const schemaFile = getSchemaFilePath(SchemaType.ListReport, entitySet?.name || entitySetName);
            const genericSchemaFile = getSchemaFilePath(SchemaType.ListReport);
            appSchemas[schemaFile.filename] = generateListReportSchemaV2(
                generateParameters,
                genericSchemas[genericSchemaFile.filename],
                entityTypeName
            );
        }
        /*--- Object Pages ----*/
        generateObjectPageSchemasV2(generateParameters, genericSchemas, appSchemas);
    } else if (templateType === TemplateType.AnalyticalListPageV2 && uiEntityTypes) {
        /*--- Analytical List Page ----*/
        const allPages = getPagesV2(generateParameters.manifest[ManifestSection.generic].pages, v2.FE_TEMPLATE_V2_ALP);

        for (const page in allPages) {
            const entitySetName = allPages[page].entitySet;
            const entitySet = getEntitySetByEntitySetName(entitySetName, generateParameters.serviceAVT);
            const entityTypeName = entitySet?.entityType?.name;
            const schemaFile = getSchemaFilePath(SchemaType.AnalyticalListPage, entitySet?.name || entitySetName);
            const genericSchemaFile = getSchemaFilePath(SchemaType.AnalyticalListPage);
            appSchemas[schemaFile.filename] = generateAnalyticalListReportSchemaV2(
                generateParameters,
                genericSchemas[genericSchemaFile.filename],
                entityTypeName
            );
        }
        /*--- Object Pages ----*/
        generateObjectPageSchemasV2(generateParameters, genericSchemas, appSchemas);
    } else if (templateType === TemplateType.OverviewPageV2) {
        /*--- Overview Page ----*/
        const schemaFile = getSchemaFilePath(SchemaType.OverviewPage);
        const genericSchemaFile = getSchemaFilePath(SchemaType.OverviewPage);
        appSchemas[schemaFile.filename] = generateOVPInterfaceV2(
            genericSchemas[genericSchemaFile.filename],
            generateParameters.manifest
        );
    }
    return { ...genericSchemas, ...appSchemas };
}

/**
 * Generates all app specific schemas for a Fiori Elements V4 application
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {Schemas} genericSchemas - The generic schemas
 * @param {UiEntityTypes} uiEntityTypes - list of all entity types, as comprised in oDataServiceAVT
 * @param {TemplateType} templateType - the given Fiori Elements' template type
 */
function generateAppSchemasV4(
    generateParameters: GenerateAppSchemaParameters,
    genericSchemas: Schemas,
    uiEntityTypes: UiEntityTypes,
    templateType: TemplateType
): Schemas {
    const appSchemas: Schemas = {};

    if (uiEntityTypes) {
        if (
            templateType === TemplateType.ListReportObjectPageV4 ||
            templateType === TemplateType.AnalyticalListPageV4
        ) {
            /*--- List Report ----*/
            const lrPages = getPagesV4(generateParameters.manifest, v4.FE_TEMPLATE_V4_LIST_REPORT);
            const alpPages = getPagesV4(generateParameters.manifest, v4.FE_TEMPLATE_V4_ALP);
            const pages = { ...lrPages, ...alpPages };
            for (const pageId in pages) {
                const entitySetName = pages[pageId].options.settings.entitySet;
                const entitySet = getEntitySetByEntitySetName(entitySetName, generateParameters.serviceAVT);
                const schemaFile = getSchemaFilePath(SchemaType.ListReport, entitySet?.name || entitySetName);
                const genericSchemaFile = getSchemaFilePath(SchemaType.ListReport);
                appSchemas[schemaFile.filename] = generateListReportSchemaV4(
                    generateParameters,
                    genericSchemas[genericSchemaFile.filename],
                    entitySet,
                    pages[pageId].name
                );
            }
            /*--- Object Pages ----*/
            generateObjectPageSchemasV4(generateParameters, genericSchemas, appSchemas);
        } else if (templateType === TemplateType.FreestylePageV4) {
            const genericSchemaFile = getSchemaFilePath(SchemaType.FreestylePage);
            appSchemas[genericSchemaFile.filename] = generateFreestyleSchemaV4(
                generateParameters,
                genericSchemas[genericSchemaFile.filename]
            );
            generateObjectPageSchemasV4(generateParameters, genericSchemas, appSchemas);
        }
    }
    return { ...genericSchemas, ...appSchemas };
}

/**
 * Generates app specific schemas by adding annotation information to copies of the generic schemas, thus replacing all generic definitions.
 * @param {GenerateAppSchemaParameters} generateParameters - list of API input parameters
 * @param {UiEntityTypes} uiEntityTypes - list of all entity types from the service metadata
 * @param templateType - type of the Fiori Elements' template
 */
async function generateAppSchemas(
    generateParameters: GenerateAppSchemaParameters,
    uiEntityTypes: UiEntityTypes,
    templateType: TemplateType
): Promise<Schemas> {
    const genericSchemas = await getGenericSchemas(templateType, generateParameters.fioriElementsVersion);

    if (generateParameters.fioriElementsVersion === FioriElementsVersion.v4) {
        return generateAppSchemasV4(generateParameters, genericSchemas, uiEntityTypes, templateType);
    } else {
        return generateAppSchemasV2(generateParameters, genericSchemas, uiEntityTypes, templateType);
    }
}

/**
 * Imports manifest and flex changes into configuration files with aid of the relevant app provider
 * @param {Manifest} manifest - manifest.json file content of the app in JSON format
 * @param {string[]} flex - list of all UI flexibility changes
 * @param {Schemas} appSchemas - list of app-specific schemas
 * @param {TemplateType} templateType - type of the Fiori Elements' template
 * @param {FileData[]} fragments - extension fragments with their file content
 * @param {ExtensionLogger} logger - logger instance for messages
 *
 * @returns {File[]} a list of all configuration files as generated by the function
 */
function importConfigs(
    manifest: Manifest,
    flex: string[],
    appSchemas: Schemas,
    templateType: TemplateType,
    fragments: FileData[],
    logger: ExtensionLogger
): File[] {
    let provider: AppProvider;
    const files: File[] = [];

    switch (templateType) {
        case TemplateType.ListReportObjectPageV2:
        case TemplateType.AnalyticalListPageV2:
            provider = new V2AppProvider(manifest, flex, appSchemas, fragments, logger);
            break;
        case TemplateType.AnalyticalListPageV4:
        case TemplateType.ListReportObjectPageV4:
            provider = new V4AppProvider(manifest, appSchemas, templateType, undefined, logger);
            break;
        case TemplateType.OverviewPageV2:
            provider = new V2OvpProvider(manifest, appSchemas, logger);
            break;
        case TemplateType.FreestylePageV4:
            provider = new V4FreestyleAppProvider(manifest, appSchemas, templateType, undefined, logger);
            break;
        default:
            break;
    }
    if (provider) {
        const { appConfig, pageConfigs } = provider.createConfigFiles();
        for (const app in appConfig) {
            files.push({ dataSourceUri: app, fileContent: JSON.stringify(appConfig[app], null, 4) });
        }
        for (const config in pageConfigs) {
            files.push({ dataSourceUri: config, fileContent: JSON.stringify(pageConfigs[config], null, 4) });
        }
    }
    return files;
}

function getFileList(schema: Schemas, configs?: File[]): File[] {
    let files: File[] = [];
    for (const schemaName of Object.keys(schema).sort()) {
        let dataSourceUri;
        if (schemaName === SchemaType.Application) {
            dataSourceUri = `${DirName.Schemas}/${FileName.App[0].toUpperCase()}${FileName.App.slice(1)}`;
        } else {
            dataSourceUri = `${DirName.Schemas}/${schemaName}.json`;
        }
        files.push({ dataSourceUri, fileContent: JSON.stringify(schema[schemaName], null, 4) });
    }
    if (configs) {
        files = files.concat(configs);
    }
    return files;
}

/**
 * Import the schema and config files for a given project
 * @param {ImportProjectParameters} importParameters - files of the project: manifest, flex changes, odata files
 */
export async function importProjectSchema(importProjectParameters: ImportProjectParameters): Promise<File[]> {
    // Initialize i18next
    initI18n();

    const fioriElementsVersion = getVersionFromManifest(importProjectParameters.manifest);
    const service = parseAndMergeAndConvert(importProjectParameters.annotations, importProjectParameters.logger);
    const uiEntityTypes = getAnnotationsForUi(service);
    const templateType = getTemplateTypeFromManifest(
        importProjectParameters.manifest,
        fioriElementsVersion,
        importProjectParameters.logger
    );
    const generateParameters: GenerateAppSchemaParameters = {
        templateType,
        manifest: importProjectParameters.manifest,
        serviceAVT: service,
        fragments: importProjectParameters.fragments,
        fioriElementsVersion,
        logger: importProjectParameters.logger
    };

    const appSchemas = await generateAppSchemas(generateParameters, uiEntityTypes, templateType);
    return getFileList(appSchemas);
}

/**
 * Import the schema and config files for a given project
 * @param {ImportProjectParameters} importParameters - files of the project: manifest, flex changes, odata files
 */
export async function importProjectSchemaAndConfig(importProjectParameters: ImportProjectParameters): Promise<File[]> {
    // Initialize i18next
    initI18n();

    let configFiles: File[] = [];
    const { manifest, annotations, flex, fragments, logger } = importProjectParameters;
    const fioriElementsVersion = getVersionFromManifest(manifest);
    const oDataServiceAVT = parseAndMergeAndConvert(annotations, logger);
    const templateType = getTemplateTypeFromManifest(manifest, fioriElementsVersion, logger);

    const generateParameters: GenerateAppSchemaParameters = {
        templateType,
        manifest: importProjectParameters.manifest,
        serviceAVT: oDataServiceAVT,
        fragments: importProjectParameters.fragments,
        fioriElementsVersion,
        logger: importProjectParameters.logger
    };
    const uiEntityTypes = getAnnotationsForUi(oDataServiceAVT);
    const appSchemas = await generateAppSchemas(generateParameters, uiEntityTypes, templateType);
    configFiles = importConfigs(manifest, flex, appSchemas, templateType, fragments, logger);
    return getFileList(appSchemas, configFiles);
}
