const MULTIEDITPATH = '/multiEdit';
export const getSmartTableControlId = (
    baseId: string,
    idBreadcrumbs: string[],
    _breadcrumbs: string[],
    controlType: ControlTypeFunction
): string => {
    if (controlType() === 'sap.m.Table') {
        return baseId + 'responsiveTable';
    } else if (controlType() === 'sap.ui.comp.smarttable.SmartTable' && baseId.includes('AnalyticalListPage')) {
        return baseId + PropertyName.table;
    }
    return buildControlIdFromParent(baseId, idBreadcrumbs);
};

//import & export createWithParameterDialog
export function importCreateParameters(manifestSection: object): object {
    if (!manifestSection['createWithParameterDialog']) {
        return undefined;
    }
    let configPart = {};
    if (manifestSection['createWithParameterDialog']) {
        configPart = JSON.parse(JSON.stringify(manifestSection['createWithParameterDialog'])) as Fields4Dialog;
    }
    return configPart;
}
export function exportCreateParameters(manifestSection: unknown, configPart: Fields4Dialog): void {
    if (configPart['createWithParameterDialog']) {
        manifestSection['createWithParameterDialog'] = JSON.parse(
            JSON.stringify(configPart['createWithParameterDialog'])
        );
    } else {
        delete manifestSection['createWithParameterDialog'];
    }
}

function exportVariants(manifestSection: object, configObject: object): void {
    for (const variant in configObject['variants']) {
        const variantConfig = configObject['variants'][variant];
        const tablesettings: string[] = ['type', 'multiSelect', 'selectAll', 'selectionLimit', 'inlineDelete'];
        for (const key in variantConfig) {
            if (tablesettings.indexOf(key) !== -1) {
                if (!variantConfig['tableSettings']) {
                    variantConfig['tableSettings'] = {};
                }
                variantConfig['tableSettings'][key] = variantConfig[key];
                delete variantConfig[key];
            }
        }
        if (manifestSection['variants']) {
            manifestSection['variants'][variant] = variantConfig;
        } else {
            manifestSection['variants'] = {
                [variant]: variantConfig
            };
        }
    }
}

function exportQuickVariantSelectionX(manifestSection: object, configObject: object): void {
    if (!configObject['variants']) {
        return;
    }
    if (Object.keys(configObject['variants']).length > 0) {
        exportVariants(manifestSection, configObject);
    } else {
        delete manifestSection['variants'];
    }
}

function importMultiEdit(manifestSection) {
    if (!manifestSection || !manifestSection['multiEdit']) {
        return undefined;
    }
    const configPart: v2.MultiEdit = JSON.parse(JSON.stringify(manifestSection['multiEdit']));

    // convert CSV format to array of strings
    configPart.ignoredFields = manifestSection['multiEdit']?.['ignoredFields']?.split(',');

    return configPart;
}

export class CreateWithFilters {
    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys) + '/createWithFilters'
        }
    })
    strategy: v2.Strategy;
}

export class MultiEdit implements v2.MultiEdit {
    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys) + MULTIEDITPATH
        }
    })
    enabled?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys) + MULTIEDITPATH
        }
    })
    annotationPath?: string;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys) + MULTIEDITPATH,
            export: convertEnumToCSV
        }
    })
    ignoredFields?: v2.IgnoredFieldsType[];
}

//import table type
function importType(manifestSection: object): string {
    if (manifestSection?.['tableSettings']?.['type']) {
        return manifestSection['tableSettings']['type'];
    } else if (manifestSection?.['tableType']) {
        return manifestSection['tableType'];
    }
}

//export table type
export function exportType(manifestSection: unknown, configPart: object): void {
    if (configPart?.['type']) {
        if (!manifestSection['tableSettings']) {
            manifestSection['tableSettings'] = {};
        }
        manifestSection['tableSettings']['type'] = configPart['type'];
        if (manifestSection?.['tableType']) {
            //old format
            delete manifestSection['tableType'];
        }
    }
}

export function deleteType(manifestSection: unknown): void {
    if (manifestSection?.['tableSettings']?.['type']) {
        delete manifestSection['tableSettings']['type'];
    }
    if (manifestSection?.['tableType']) {
        //old format
        delete manifestSection['tableType'];
    }
}

