export class CommonTableSettings<COLS = v2.GenericColumns> implements v2.CommonTableSettings<COLS> {
    getDeprecated = (): DeprecatedProperties => {
        return {
            tableType: {
                since: '1.50.0',
                deprecated: {
                    since: '1.70.0',
                    text: 'Use tableSettings.type instead'
                }
            },
            enableAutoBinding: {
                since: '1.40.0',
                deprecated: {
                    since: '1.75.2',
                    text: 'Use tableSettings.loadDataOnAppLaunch instead'
                }
            }
        };
    };

    @syncRule({
        manifest: {
            key: 'createWithParameterDialog',
            path: (pageKeys): string => getTableSettingPathV2(pageKeys),
            import: importCreateParameters,
            export: exportCreateParameters
        }
    })
    @validity({
        since: '1.77.0'
    })
    createWithParameterDialog?: Fields4Dialog;

    @syncRule({
        flex: {
            controlId: buildControlIdFromParent,
            controlType: (): ControlType => ControlType.SmartTable,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.38.0'
    })
    showTablePersonalisation?: boolean;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.SmartTable,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.26.0'
    })
    useExportToExcel?: boolean;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.SmartTable,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.50.0'
    })
    exportType?: smarttable.ExportType;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.SmartTable,
            createFlexChange: exportToFlexChange
        }
    })
    fitContainer?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.48.0'
    })
    quickVariantSelection?: QuickVariantSelection;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.48.0'
    })
    quickVariantSelectionX?: QuickVariantSelectionX;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.38.0'
    })
    multiSelect?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.70.0'
    })
    selectAll?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.70.0'
    })
    selectionLimit?: number;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.75.2'
    })
    dataLoadSettings?: DataLoadSettings;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.42.0'
    })
    condensedTableLayout?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.56.0'
    })
    enableTableFilterInPageVariant?: boolean;

    @childId('toolBar')
    toolBar?: v2.ToolBar;
}
export class ResponsiveTable extends CommonTableSettings implements v2.ResponsiveTable {
    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys),
            import: importType,
            export: exportType,
            delete: deleteType
        }
    })
    @validity({
        since: '1.50.0'
    })
    type?: v2.TableTypeV2.ResponsiveTable;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getAppSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.54.0'
    })
    createWithFilters?: CreateWithFilters;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.Table,
            createFlexChange: exportToFlexChange
        },
        generate: addPatternForBindingChangeOfEnumLR
    })
    @validity({
        since: '1.52.0'
    })
    popinLayout?: PopinLayout;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.Table,
            createFlexChange: exportToFlexChange
        }
    })
    includeItemInSelection?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys)
        }
    })
    @validity({
        since: '1.77.0'
    })
    inlineDelete?: boolean;

    @syncRule({
        manifest: {
            path: (): string => GENERICAPPSETTINGS
        }
    })
    @validity({
        since: '1.63.0'
    })
    showDraftToggle?: boolean;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.Table,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.16.0'
    })
    growingThreshold?: number;

    @syncRule({
        manifest: {
            path: (pageKeys): string => getTableSettingPathV2(pageKeys),
            import: importMultiEdit
        }
    })
    @validity({
        since: '1.98.0'
    })
    multiEdit?: MultiEdit;
}

