export class ALPResponsiveTable extends TableALP {
    /*No sync rule needed for type as it is already part of TableALP. This also caused sync issues with flexChanges in release 1.60 as tabletype is not explicity set to ResponsiveTable in 1.60
    During import, Table type is used in function "addFlex" to refer to correct definition in schema.
    */
    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.Table,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.16.0'
    })
    growingThreshold?: number;
}

