import type { v4, Manifest } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getActionsPathForLR, getActionsPathForView } from '../../application';
import { addPatternForBindingChangeOfEnumLR } from '../../../common/decoration/control';

export class ToolBarAction implements v4.ToolBarAction {
    @syncRule({
        manifest: {
            path: getActionsPathForLR
        }
    })
    enableOnSelect?: v4.SelectType;
}

export class ViewToolBarAction implements v4.ViewToolBarAction {
    @syncRule({
        manifest: {
            path: getActionsPathForView
        }
    })
    enableOnSelect?: v4.SelectType;
}

/**
 * Sync class for Position
 */
export class CustomActionPosition implements v4.CustomActionPosition {
    @syncRule({
        manifest: {
            path: (
                pageName: string[],
                manifest: Manifest,
                sectionId: string,
                columnKey: string,
                targetAnnotation: string
            ): string => getActionsPathForLR(pageName, manifest, sectionId, columnKey, targetAnnotation) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    anchor?: string;
    @syncRule({
        manifest: {
            path: (
                pageName: string[],
                manifest: Manifest,
                sectionId: string,
                columnKey: string,
                targetAnnotation: string
            ): string => getActionsPathForLR(pageName, manifest, sectionId, columnKey, targetAnnotation) + '/position'
        }
    })
    @validity({
        since: '1.84.0'
    })
    placement: v4.ActionPlacement;
}

export class CustomAction implements v4.CustomAction {
    @syncRule({
        manifest: {
            path: getActionsPathForLR
        }
    })
    @validity({
        since: '1.84.0'
    })
    text: string;

    @syncRule({
        manifest: {
            path: getActionsPathForLR
        }
    })
    @validity({
        since: '1.84.0'
    })
    position: CustomActionPosition;

    @syncRule({
        manifest: {
            path: getActionsPathForLR
        }
    })
    @validity({
        since: '1.84.0'
    })
    press: string;

    @syncRule({
        manifest: {
            path: getActionsPathForLR
        }
    })
    @validity({
        since: '1.84.0'
    })
    requiresSelection?: boolean;

    @syncRule({
        manifest: {
            path: getActionsPathForLR
        },
        generate: addPatternForBindingChangeOfEnumLR
    })
    @validity({
        since: '1.84.0'
    })
    visible?: boolean;

    @syncRule({
        manifest: {
            path: getActionsPathForLR
        },
        generate: addPatternForBindingChangeOfEnumLR
    })
    @validity({
        since: '1.84.0'
    })
    enabled?: boolean;
}

