import type { Manifest, v2, Files } from '@sap/ux-specification-types';
import { FioriElementsVersion, PageType, ManifestSection, SchemaType } from '@sap/ux-specification-types';
import { AppProvider } from '../../../common/appProvider';
import { transferSettingsOfObject } from '../common';
import { createOverviewPageConfig } from '../pages/overviewPage';
import { AppSettings } from '../../application';
import { getSchemaFilePath, generatePageId } from '../../../common';
import type { ExtensionLogger, ImportOverviewPageV2Parameters } from '../../../..';

export class V2OvpProvider extends AppProvider {
    public constructor(manifest: Manifest, schemas: Files, logger?: ExtensionLogger) {
        const pages: v2.Pages = {};
        const sapOvp = manifest[ManifestSection.ovp];
        const appSettings = new AppSettings();
        const schemaFile = getSchemaFilePath(SchemaType.Application);
        transferSettingsOfObject(
            appSettings,
            manifest,
            schemas[schemaFile.filename]['definitions']['AppSettings'],
            undefined
        );
        const jsonSchema: string = Object.keys(schemas).find((element) => element.includes(PageType.OverviewPage));
        const parameters: ImportOverviewPageV2Parameters = {
            manifest,
            jsonSchema: schemas[jsonSchema]
        };
        const page: v2.Page = {
            pageType: PageType.OverviewPage,
            config: createOverviewPageConfig(parameters),
            entitySet: sapOvp.globalFilterEntitySet
        };
        const id = generatePageId(page.pageType, page.entitySet);
        pages[id] = page;
        super(manifest, schemas, id, pages, FioriElementsVersion.v2, appSettings, logger);
    }
}
