import type { v2, DeprecatedProperties, ControlTypeFunction } from '@sap/ux-specification-types';
import { ControlType, DefinitionName, QUICKVARPATH } from '@sap/ux-specification-types';
import {
    syncRule,
    validity,
    buildControlIdFromParent,
    exportToFlexChange,
    convertSectionId,
    childId
} from '../../../common/decoration';
import { getAppSettingOBJsectionPathV2, getTableSettingsOBJsectionPathV2 } from '../../application';
import { addPatternForBindingChangeOfEnumOP } from '../../../common';
import { exportCreateParameters } from './Table';
import type { PopinLayout } from 'sap/m/library';
import type { smarttable } from 'sap/ui/comp/library';

const getSmartTableControlId = (
    baseId: string,
    _idBreadcrumbs: string[],
    breadcrumbs: string[],
    controlType: ControlTypeFunction,
    title: string
): string => {
    const breadCrumbsCopy = breadcrumbs.slice(1, 2);
    let suffixId: string;

    if (controlType() === 'sap.ui.comp.smarttable.SmartTable') {
        suffixId = DefinitionName.Table;
    } else if (controlType() === 'sap.m.Table') {
        suffixId = 'responsiveTable';
    }
    breadCrumbsCopy.push(suffixId);
    breadCrumbsCopy[0] = convertSectionId(breadCrumbsCopy[0], title);
    return buildControlIdFromParent(baseId, breadCrumbsCopy);
};

export class QuickVariantSelectionOP {
    @syncRule({
        manifest: {
            path: (pageKeys, manifest, sectionId): string =>
                getAppSettingOBJsectionPathV2(pageKeys, manifest, sectionId) + QUICKVARPATH
        }
    })
    @validity({
        since: '1.48.0'
    })
    showCounts?: boolean;

    @syncRule({
        manifest: {
            path: (pageKeys, manifest, sectionId): string =>
                getAppSettingOBJsectionPathV2(pageKeys, manifest, sectionId) + QUICKVARPATH
        }
    })
    @validity({
        since: '1.48.0'
    })
    variants: {
        [key: number]: v2.QuickVariant;
    };
}

export class FieldPathOP implements v2.FieldPathOP {
    @syncRule({
        manifest: {
            path: (pageKeys, manifest, sectionId, key): string =>
                getTableSettingsOBJsectionPathV2(pageKeys, manifest, sectionId) +
                `/createWithParameterDialog/fields/${key}`
        }
    })
    @validity({
        since: '1.77.0'
    })
    path: string;
}

export class Fields4DialogOP implements v2.Fields4Dialog {
    @syncRule({
        manifest: {
            path: (pageKeys, manifest, sectionId): string =>
                getTableSettingsOBJsectionPathV2(pageKeys, manifest, sectionId) + '/createWithParameterDialog'
        }
    })
    @validity({
        since: '1.77.0'
    })
    fields: v2.CreationFieldKeys;
}

function importCreateParameters(manifestSection: object): object {
    let configPart;
    if (manifestSection['createWithParameterDialog']) {
        configPart = JSON.parse(JSON.stringify(manifestSection['createWithParameterDialog'])) as Fields4DialogOP;
    }
    return configPart;
}
export class ObjectPageCommonTableSettings<COLS = v2.GenericColumns> implements v2.ObjectPageCommonTableSettings<COLS> {
    getDeprecated = (): DeprecatedProperties => {
        return {
            tableType: {
                since: '1.50.0',
                deprecated: {
                    since: '1.70.0',
                    text: 'Use tableSettings.type instead'
                }
            }
        };
    };

    @syncRule({
        manifest: {
            key: 'createWithParameterDialog',
            path: getTableSettingsOBJsectionPathV2,
            import: importCreateParameters,
            export: exportCreateParameters
        }
    })
    @validity({
        since: '1.77.0'
    })
    createWithParameterDialog?: Fields4DialogOP;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.SmartTable,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.28.0'
    })
    editable?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsOBJsectionPathV2
        }
    })
    @validity({
        since: '1.54.0'
    })
    multiSelect?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsOBJsectionPathV2
        }
    })
    @validity({
        since: '1.70.0'
    })
    selectAll?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsOBJsectionPathV2
        }
    })
    @validity({
        since: '1.70.0'
    })
    selectionLimit?: number;

    @syncRule({
        manifest: {
            path: getAppSettingOBJsectionPathV2
        }
    })
    @validity({
        since: '1.63.0'
    })
    quickVariantSelection?: QuickVariantSelectionOP;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.SmartTable,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.26.0'
    })
    useExportToExcel?: boolean;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.SmartTable,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.50.0'
    })
    exportType?: smarttable.ExportType;

    @syncRule({
        manifest: {
            path: getAppSettingOBJsectionPathV2
        }
    })
    @validity({
        since: '1.48.5'
    })
    createMode?: v2.CreateMode;

    @syncRule({
        manifest: {
            path: getAppSettingOBJsectionPathV2
        }
    })
    @validity({
        since: '1.61.0'
    })
    disableDefaultInlineCreateSort?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsOBJsectionPathV2
        }
    })
    @validity({
        since: '1.78.0'
    })
    variantManagement?: boolean;

    @childId('toolBar')
    toolBar?: v2.ObjectPageToolBar;
}

export class ObjectPageResponsiveTable extends ObjectPageCommonTableSettings implements v2.ObjectPageResponsiveTable {
    @syncRule({
        manifest: {
            path: getTableSettingsOBJsectionPathV2
        }
    })
    type?: v2.TableTypeV2.ResponsiveTable;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.Table,
            createFlexChange: exportToFlexChange
        }
    })
    includeItemInSelection?: boolean;

    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.Table,
            createFlexChange: exportToFlexChange
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    @validity({
        since: '1.52.0'
    })
    popinLayout?: PopinLayout;

    @syncRule({
        manifest: {
            path: getTableSettingsOBJsectionPathV2
        }
    })
    @validity({
        since: '1.77.0'
    })
    inlineDelete?: boolean;
    @syncRule({
        flex: {
            controlId: getSmartTableControlId,
            controlType: (): ControlType => ControlType.Table,
            createFlexChange: exportToFlexChange
        }
    })
    @validity({
        since: '1.16.0'
    })
    growingThreshold?: number;
}

