import type { Manifest } from '@sap/ux-specification-types';
import { v2, ManifestSection, PageType } from '@sap/ux-specification-types';
import { getReflectMetadata } from '../../../common/decoration/decorators';
import { ensureManifestSectionByPathV2 } from '../../export/manifest';
import { MetadataInstanceFactoryV2 as Factory } from '../../export/factory';
import type { MetadataInstanceInterface } from '../../../common/decoration/factory';
import { assignReflectMetadataRules } from '../../export/export';
import { importProperty } from '../../../common';
import type { ImportOverviewPageV2Parameters } from '../../../..';

/**
 * Transfers the card settings from manifest to config
 * @param propertyKey - Key of the property or setting
 * @param manifestSection - Section in manifest.json
 * @param configPart - Part of the config JSON
 * @param pathHierarchy - Parts of the path or breadcrumbs
 */
function transferCardSettings(
    propertyKey: string,
    manifestSection: object,
    configPart: { [key: string]: any },
    pathHierarchy?: string[]
): void {
    if (propertyKey === 'settings') {
        switch (manifestSection['template']) {
            case 'sap.ovp.cards.charts.analytical':
                configPart[pathHierarchy[0]][v2.CardSettingsType.analyticalCardSettings] = manifestSection[propertyKey];
                break;
            case 'sap.ovp.cards.v4.charts.analytical':
                configPart[pathHierarchy[0]][v2.CardSettingsType.analyticalCardSettingsv4] =
                    manifestSection[propertyKey];
                break;
            case 'sap.ovp.cards.list':
            case 'sap.ovp.cards.v4.list':
                configPart[pathHierarchy[0]][v2.CardSettingsType.listCardSettings] = manifestSection[propertyKey];
                break;
            case 'sap.ovp.cards.stack':
                configPart[pathHierarchy[0]][v2.CardSettingsType.stackCardSettings] = manifestSection[propertyKey];
                break;
            case 'sap.ovp.cards.linklist':
            case 'sap.ovp.cards.v4.linklist':
                configPart[pathHierarchy[0]][v2.CardSettingsType.linkListCardSettings] = manifestSection[propertyKey];
                break;
            case 'sap.ovp.cards.table':
            case 'sap.ovp.cards.v4.table':
                configPart[pathHierarchy[0]][v2.CardSettingsType.tableCardSettings] = manifestSection[propertyKey];
                break;
            default:
                configPart[pathHierarchy[0]].settings = manifestSection[propertyKey];
                break;
        }
    } else {
        configPart[pathHierarchy[0]][propertyKey] = manifestSection[propertyKey];
    }
}

/**
 * Handles a list of properties of a given part of the schema, transfers the relevant property values or settings from manifest to config.
 * @param configPart - Part of the config JSON
 * @param manifest - manifest.json or app descriptor
 * @param appSchema - app specific JSON schema
 * @param pathHierarchy - Parts of the path or breadcrumbs
 */
function transferSettingsOfObject(
    configPart: { [key: string]: any },
    manifest: Manifest,
    appSchema: object,
    pathHierarchy?: string[]
): void {
    for (const propertyKey in appSchema) {
        const syncRule = getReflectMetadata(configPart, propertyKey);
        if (syncRule?.manifest) {
            const path = syncRule.manifest.path([...pathHierarchy, propertyKey]);
            const manifestSection = ensureManifestSectionByPathV2(manifest, path);
            if (Object.prototype.hasOwnProperty.call(manifestSection, propertyKey)) {
                if (pathHierarchy.length !== 0) {
                    transferCardSettings(propertyKey, manifestSection, configPart, pathHierarchy);
                } else {
                    importProperty(syncRule, manifestSection, propertyKey, configPart);
                }
            }
        }
    }
}

/**
 * OVP: Transfers all settings from manifest to config
 * @param pageConfig - the OVP configuration (JSON)
 * @param manifest  - manifest.json or app descriptor
 * @param appSchema - app specific JSON schema
 * @param factory - Metadata factory
 */
function addSettings(
    pageConfig: v2.OverviewPageConfigV2,
    manifest: Manifest,
    appSchema: object,
    factory: MetadataInstanceInterface
): void {
    transferSettingsOfObject(pageConfig, manifest, appSchema['properties'], []);

    pageConfig.cards = {};

    Object.keys(manifest[ManifestSection.ovp]['cards']).forEach((newKey) => {
        const cardTemplate = manifest[ManifestSection.ovp]['cards'][newKey].template;
        const cardType = v2.cardTemplateTypeMap[cardTemplate];

        //instantiate Card:
        pageConfig.cards[newKey] = assignReflectMetadataRules(factory, cardType, {}, 'CustomCard') as v2.CardType;

        pageConfig.cards = assignReflectMetadataRules(factory, cardType, pageConfig.cards, 'CustomCard') as {
            [k: string]: v2.CardType;
        };

        //transfer settings:
        transferSettingsOfObject(pageConfig.cards, manifest, manifest[ManifestSection.ovp]['cards'][newKey], [newKey]);
    });
}

/**
 * Creates the configuration (file content) for an overview page
 * @param {object} manifest  - manifest of the app
 * @param {object} jsonSchema - app specific JSON schema
 *
 * @returns {object} - the OVP configuration (JSON)
 */
export function createOverviewPageConfig(importParameters: ImportOverviewPageV2Parameters): object {
    const factory = new Factory();
    //Instantiate Page
    const config = factory.createPageInstance(PageType.OverviewPage);

    if (importParameters.jsonSchema) {
        addSettings(config, importParameters.manifest, importParameters.jsonSchema, factory);
    }
    return config;
}
