import type { v2, ControlTypeFunction } from '@sap/ux-specification-types';
import { ControlType } from '@sap/ux-specification-types';
import { syncRule, exportToFlexChange, buildControlIdFromParent } from '../../../common/decoration';
import { addPatternForBindingChangeOfEnumOP, convertActionKeyToStableId } from '../../../common';
import type { ButtonType } from 'sap/m/library';
import type { URI } from 'sap/ui/core/library';

/**
 * Create stable ID for Object Page Header Action button
 *
 * @param {string} baseId Base Id of the control
 * @param {string[]} idBreadcrumbs
 * @param {string[]} breadcrumbs
 * @param {ControlTypeFunction} controlType
 * @returns {string} stable ID of Object Page Header Action button
 */
const getActionButtonId = (
    baseId: string,
    idBreadcrumbs: string[],
    breadcrumbs: string[],
    controlType: ControlTypeFunction
): string => {
    if (controlType() === ControlType.Button || controlType() === ControlType.HeaderAction) {
        const actionId = breadcrumbs[2];
        return `${baseId}action::${convertActionKeyToStableId(actionId)}`;
    }
    return buildControlIdFromParent(baseId, idBreadcrumbs);
};

const flexRuleForHeaderAction = {
    controlId: getActionButtonId,
    controlType: (): ControlType => ControlType.HeaderAction,
    createFlexChange: exportToFlexChange
};

export class HeaderAction implements v2.HeaderAction {
    @syncRule({
        flex: flexRuleForHeaderAction,
        generate: addPatternForBindingChangeOfEnumOP
    })
    visible: boolean;

    @syncRule({
        flex: flexRuleForHeaderAction
    })
    tooltip?: string;

    @syncRule({
        flex: flexRuleForHeaderAction
    })
    icon?: URI;

    @syncRule({
        flex: flexRuleForHeaderAction
    })
    activeIcon?: URI;

    @syncRule({
        flex: flexRuleForHeaderAction
    })
    type?: ButtonType;
}
