import i18next from 'i18next';
import _unset from 'lodash.unset';
import Ajv from 'ajv';
import type {
    UI5Version,
    ExportParametersV2,
    ExportAnalyticalListPageV2Parameters,
    ExportListReportV2Parameters,
    ExportObjectPageV2Parameters,
    ExportOverviewPageV2Parameters
} from '../../..';
import type { ExportResults, FileData, FioriElementsVersion } from '@sap/ux-specification-types';
import {
    v2,
    PageType,
    SchemaType,
    SAPUI5_FRAGMENT_CLASS,
    MANIFESTPATH,
    ManifestSection,
    LogSeverity
} from '@sap/ux-specification-types';
import type { ObjectPage } from './pages';
import { getPageLayoutInformation, getReflectMetadata } from '../../common';
import { ApplicationV2, AppSettings } from '../application';
import { ensureManifestSectionByPathV2, transformPageToNewManifestV2 } from './manifest';
import { findListReportPageV2, findAnalyticalListPage, findObjectPageV2 } from '../import';
import { MetadataInstanceFactoryV2 as Factory } from './factory';
import { transferManifestEntriesAndFlexChange, transferManifestEntriesOVP } from './export';
import type { MetadataInstanceInterface } from '../../common/decoration/factory';
import type { TransferParameterTypeV2 } from '../types';
import { log } from '../../../extensionLogger';

/**
 * Returns a fresh export results object with default values
 */
const getDefaultExportResult = (fragmentFiles?: FileData[]): ExportResults => ({
    flexChanges: [],
    manifest: {
        [ManifestSection.generic]: { pages: {} }
    },
    fragments: fragmentFiles ? fragmentFiles : []
});

/**
 * Instantiates the export classes for a single object page section
 * @param currentSection - Export class as an object, to be updated
 * @param sectionId - Current section ID
 * @param {MetadataInstanceInterface} factory - factory for creating metadata instances
 */
function getClassesForSingleSection(
    currentSection: object,
    sectionId: string,
    factory: MetadataInstanceInterface
): void {
    if (currentSection && sectionId === 'custom') {
        // Custom sections
        for (const index in currentSection) {
            if (currentSection[index].className === SAPUI5_FRAGMENT_CLASS) {
                currentSection[index] = factory.createInstance(
                    PageType.ObjectPage,
                    'ObjectPageCustomSectionFragment',
                    currentSection[index]
                );
            } else {
                currentSection[index] = factory.createInstance(
                    PageType.ObjectPage,
                    'ObjectPageCustomSectionView',
                    currentSection[index]
                );
            }
        }
    }
}

/**
 * Recursive sub-function for handling subsections
 * @param currentSection - Object page section in config
 */
function getSubsectionClasses(currentSection: object, factory: MetadataInstanceInterface): void {
    if (currentSection?.['subsections']) {
        let subSection: object;

        for (const subSectionId in currentSection['subsections']) {
            subSection = currentSection['subsections'][subSectionId];
            getClassesForSingleSection(subSection, subSectionId, factory);
            getSubsectionClasses(subSection, factory);
        }
    }
}

/**
 * Instantiates the export classes for the object page section
 * @param objectPageConfig - the given object page configuration
 * @param objectPage - the overall object page export class where the sections shall be included
 * @param {MetadataInstanceInterface} factory - factory for creating metadata instances
 */
function getExportClassesForSections(
    objectPageConfig: v2.ObjectPageConfigV2,
    objectPage: ObjectPage,
    factory: MetadataInstanceInterface
): void {
    // Main function
    let section: object;

    for (const sectionId in objectPageConfig.sections) {
        section = objectPage.sections[sectionId];
        getClassesForSingleSection(section, sectionId, factory);
        getSubsectionClasses(section, factory);
    }
}

/**
 * Run through the given ListReport config and return respective manifest entry and flex changes
 * @param {ExportListReportV2Parameters} exportParameters - all API parameters needed for the export
 * @param ui5Version - SAP UI5 version
 *
 * @returns ExportResults - The export result comprises the enhanced manifest as well as a list of flex changes.
 */
function exportListReportPage(exportParameters: ExportListReportV2Parameters, ui5Version: UI5Version): ExportResults {
    const listReportConfig = exportParameters.page.config as v2.ListReportConfigV2;
    const entitySet = exportParameters.page.entitySet;
    const exportResults: ExportResults = getDefaultExportResult(exportParameters.fragments);
    const factory = new Factory();
    const listReport = factory.createPageInstance(PageType.ListReport, listReportConfig);
    const pageLayoutInformation = getPageLayoutInformation(listReport);
    const baseId = `${exportParameters.appId}::${pageLayoutInformation.id}::${entitySet}--`;
    const pageKeys: string[] = [];
    const v2Page = findListReportPageV2(exportParameters.manifest[ManifestSection.generic].pages, pageKeys);
    if (!v2Page) {
        log(exportParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOLR'),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.generic]
            }
        });
        return;
    }
    if (!v2Page.component.settings) {
        v2Page.component.settings = {};
    }
    exportResults.manifest = JSON.parse(JSON.stringify(exportParameters.manifest));

    if (listReportConfig && Object.keys(listReportConfig).length !== 0) {
        const transferParameters: TransferParameterTypeV2 = {
            v2Page,
            exportResults,
            appId: exportParameters.appId,
            baseId,
            appSchema: exportParameters.jsonSchema,
            ui5Version,
            factory,
            pageKeys,
            pageType: PageType.ListReport,
            logger: exportParameters.logger
        };

        transferManifestEntriesAndFlexChange(
            transferParameters,
            listReport,
            [],
            [],
            exportParameters.jsonSchema //starting point for target definition
        );
    }

    return exportResults;
}

/**
 * Run through the given AnalyticalListPage config and return respective manifest entry and flex changes
 * @param {ExportAnalyticalListPageV2Parameters} exportParameters - all API parameters needed for the export
 * @param ui5Version - SAP UI5 version
 *
 * @returns ExportResults - The export result comprises the enhanced manifest as well as a list of flex changes.
 */
function exportAnalyticalListPage(
    exportParameters: ExportAnalyticalListPageV2Parameters,
    ui5Version: UI5Version
): ExportResults {
    const analyticalListPageConfig = exportParameters.page.config;
    const entitySet = exportParameters.page.entitySet;
    const exportResults: ExportResults = getDefaultExportResult(exportParameters.fragments);
    exportResults.flexChanges = [];
    exportResults.manifest = JSON.parse(JSON.stringify(exportParameters.manifest));
    const factory = new Factory();
    //Instantiate Page
    const analyticalListPage = factory.createPageInstance(PageType.AnalyticalListPage, analyticalListPageConfig);

    //Instantiate base ID
    const pageLayoutInformation = getPageLayoutInformation(analyticalListPage);
    const baseId = `${exportParameters.appId}::${pageLayoutInformation.id}::${entitySet}--`;
    const pageKeys: string[] = [];

    const v2Page = findAnalyticalListPage(
        exportParameters.manifest[ManifestSection.generic].pages,
        pageKeys,
        exportParameters.logger
    );
    if (!v2Page) {
        return;
    }
    if (!v2Page.component.settings) {
        v2Page.component.settings = {};
    }
    exportResults.manifest = JSON.parse(JSON.stringify(exportParameters.manifest));

    if (analyticalListPageConfig && Object.keys(analyticalListPageConfig).length !== 0) {
        const transferParameters: TransferParameterTypeV2 = {
            v2Page,
            exportResults,
            appId: exportParameters.appId,
            baseId,
            appSchema: exportParameters.jsonSchema,
            ui5Version,
            factory,
            pageKeys,
            pageType: PageType.AnalyticalListPage,
            logger: exportParameters.logger
        };

        transferManifestEntriesAndFlexChange(
            transferParameters,
            analyticalListPage,
            [],
            [],
            exportParameters.jsonSchema //starting point for target definition
        );
    }

    return exportResults;
}

/**
 * Run through the given ObjectPage config and return respective manifest entry and flex changes
 * @param {ExportObjectPageV2Parameters} exportParameters - all API parameters needed for the export
 * @param ui5Version - SAP UI5 version
 *
 * @returns ExportResults - The export result comprises the enhanced manifest as well as a list of flex changes.
 */
function exportObjectPage(exportParameters: ExportObjectPageV2Parameters, ui5Version: UI5Version): ExportResults {
    const objectPageConfig = exportParameters.page.config as v2.ObjectPageConfigV2;
    const entitySet = exportParameters.page.entitySet;
    const exportResults: ExportResults = getDefaultExportResult(exportParameters.fragments);
    const factory = new Factory();
    const objectPage = factory.createPageInstance(PageType.ObjectPage, objectPageConfig);
    exportResults.manifest = JSON.parse(JSON.stringify(exportParameters.manifest));
    if (objectPageConfig) {
        const pageLayoutInformation = getPageLayoutInformation(objectPage);
        const baseId = `${exportParameters.appId}::${pageLayoutInformation.id}::${entitySet}--`;

        const pageKeys: string[] = [];
        const v2Page: v2.SapUiAppPageV2 = findObjectPageV2(
            exportParameters.manifest[ManifestSection.generic].pages,
            entitySet,
            pageKeys
        );
        if (!v2Page) {
            return exportResults;
        }
        if (objectPageConfig.sections) {
            getExportClassesForSections(objectPageConfig, objectPage, factory);
        }
        if (Object.keys(objectPageConfig).length !== 0) {
            const transferParameters: TransferParameterTypeV2 = {
                v2Page,
                exportResults,
                appId: exportParameters.appId,
                baseId,
                appSchema: exportParameters.jsonSchema,
                ui5Version,
                factory,
                pageKeys,
                pageType: PageType.ObjectPage,
                logger: exportParameters.logger
            };

            transferManifestEntriesAndFlexChange(
                transferParameters,
                objectPage,
                [],
                [],
                exportParameters.jsonSchema //starting point for target definition
            );
        }
    }
    return exportResults;
}
/**
 * Run through the given ObjectPage config and return respective manifest entry and flex changes
 * @param {ExportOverviewPageV2Parameters} exportParameters - all API parameters needed for the export
 *
 * @returns ExportResults - The export result comprises the enhanced manifest as well as a list of flex changes.
 */
const exportOverviewPage = (exportParameters: ExportOverviewPageV2Parameters): ExportResults => {
    const exportResults: ExportResults = getDefaultExportResult();
    exportResults.manifest = JSON.parse(JSON.stringify(exportParameters.manifest));
    const factory = new Factory();
    const overviewPage = factory.createPageInstance(PageType.OverviewPage, exportParameters.page.config);

    const ajv = new Ajv({ allErrors: true });
    const validate = ajv.compile(exportParameters.jsonSchema);

    if (!validate(overviewPage)) {
        for (const err of validate.errors) {
            const additionalProperty = err.params['additionalProperty'];
            if (
                err.keyword === 'additionalProperties' &&
                !Object.values(v2.CardSettingsType).includes(err.params['additionalProperty']) &&
                additionalProperty !== 'settings'
            ) {
                _unset(overviewPage, `${err.dataPath.slice(1)}.${additionalProperty}`);
            }
        }
    }
    if (exportParameters.page.config && Object.keys(exportParameters.page.config).length !== 0) {
        transferManifestEntriesOVP(
            overviewPage,
            exportResults,
            exportParameters.jsonSchema,
            exportParameters.jsonSchema,
            factory,
            []
        );
    }

    return exportResults;
};

/**
 * Exports a Fiori Element V2 application.
 * It converts the entries of the input application (config file) to corresponding manifest settings and flex changes
 * @param application - content of the config file of an application
 * @param manifest - app descriptor (manifest.json)
 * @param jsonSchema - application specific schema
 * @returns ExportResults - The export result comprises the enhanced manifest as well as a list of flex changes.
 */
const exportApplicationV2 = (application: ApplicationV2, manifest: object, jsonSchema: object): ExportResults => {
    const exportResults: ExportResults = getDefaultExportResult();
    exportResults.manifest = JSON.parse(JSON.stringify(manifest));

    //transfer application settings
    const applicationV2 = Object.assign(new ApplicationV2(), application);
    applicationV2.settings = Object.assign(new AppSettings(), application['settings']);
    for (const key in jsonSchema['definitions']['AppSettings']['properties']) {
        const syncRule = getReflectMetadata(applicationV2.settings, key);
        if (syncRule && syncRule.manifest) {
            const path = syncRule.manifest.path();
            const manifestSection = ensureManifestSectionByPathV2(exportResults.manifest, path);
            if (application['settings'] && application['settings'][key] !== undefined) {
                manifestSection[key] = application['settings'][key];
            } else {
                delete manifestSection[key];
            }
        }
    }
    // transfer pages and routings
    const manifestPages = exportResults.manifest[ManifestSection.generic]?.pages;
    if (manifestPages && application['pages']) {
        transformPageToNewManifestV2(application.home, application['pages'], Object.values(manifestPages)[0] as object);
    }
    return exportResults;
};

/**
 * Exports a Fiori Element V2 page.
 * It converts the entries of the input page (config file) to corresponding manifest settings and flex changes
 * @param exportParametersV2 - list of parameters for the export; the list varies for the different scgema types
 *
 * @returns ExportResults - The export result comprises the enhanced manifest as well as a list of flex changes.
 */
export function exportPageV2(
    exportParametersV2: ExportParametersV2[FioriElementsVersion.v2],
    ui5Version: UI5Version
): ExportResults | undefined {
    if (exportParametersV2[SchemaType.ListReport]) {
        return exportListReportPage(exportParametersV2[SchemaType.ListReport], ui5Version);
    } else if (exportParametersV2[SchemaType.ObjectPage]) {
        return exportObjectPage(exportParametersV2[SchemaType.ObjectPage], ui5Version);
    } else if (exportParametersV2[SchemaType.OverviewPage]) {
        return exportOverviewPage(exportParametersV2[SchemaType.OverviewPage]);
    } else if (exportParametersV2[SchemaType.Application]) {
        const { application, manifest, jsonSchema } = exportParametersV2[SchemaType.Application];
        return exportApplicationV2(application, manifest, jsonSchema);
    } else if (exportParametersV2[SchemaType.AnalyticalListPage]) {
        return exportAnalyticalListPage(exportParametersV2[SchemaType.AnalyticalListPage], ui5Version);
    }
}
