import { determineTableType, transferSettingsOfObject, addFlex, addFragments, convertActionKey } from '../common/index';
import type { v2, ImportListReportV2Parameters } from '@sap/ux-specification-types';
import { LogSeverity, DefinitionName, MANIFESTPATH, ManifestSection, PageType } from '@sap/ux-specification-types';
import { parseChangeString } from '../../../common';
import { log } from '../../../../extensionLogger';
import i18next from 'i18next';
import { initI18n } from '../../../../i18n/i18n';
import { findListReportPageV2, removeEmptySettings } from '../../import/utils';
import { MetadataInstanceFactoryV2 as Factory } from '../../export/factory';
import type { MetadataInstanceInterface } from '../../../common/decoration/factory';
import { INLINEDELETETABLE, MULTISELECTTABLE } from '../../types';
import type { Definition } from 'typescript-json-schema';

/**
 * Imports filterbar information into the LR config
 * @param {ListReportConfig} listReportConfig - LR config, to be filled
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {ImportListReportV2Parameters} importParameters  - object comprising all input data
 * @param {Definition} schemaDefinitions - app schema definitions
 * @param {string} pageKey - key of the page in manifest
 */
function importFilterBar(
    listReportConfig: v2.ListReportConfigV2,
    factory: MetadataInstanceInterface,
    importParameters: ImportListReportV2Parameters,
    schemaDefinitions: Definition,
    pageKey: string
) {
    listReportConfig.filterBar = factory.createInstance(
        PageType.ListReport,
        DefinitionName.ListReportFilterBar,
        listReportConfig.filterBar
    );
    transferSettingsOfObject(
        listReportConfig.filterBar,
        importParameters.manifest,
        schemaDefinitions[DefinitionName.ListReportFilterBar],
        pageKey,
        importParameters.logger
    );
    if (schemaDefinitions['SelectionFields']) {
        listReportConfig.filterBar.selectionFields = factory.createInstance(
            PageType.ListReport,
            DefinitionName.ListReportFilterBar,
            listReportConfig.filterBar.selectionFields
        );
        const schemaSelectionFields = schemaDefinitions['SelectionFields'].properties;
        for (const selectionProperty in schemaSelectionFields) {
            const selectionKey = selectionProperty;
            listReportConfig.filterBar.selectionFields[selectionKey] = factory.createInstance(
                PageType.ListReport,
                'SelectionFieldDateSettings'
            );
            transferSettingsOfObject(
                listReportConfig.filterBar.selectionFields[selectionKey],
                importParameters.manifest,
                schemaDefinitions['SelectionFieldDateSettings'],
                pageKey,
                importParameters.logger,
                selectionKey
            );
        }
    }
}

/**
 * Imports table information into the LR config
 * @param {TableTypeV2} tableType - table type
 * @param {Definition} schemaDefinitions - app schema definitions
 * @param {ListReportConfig} listReportConfig - LR config, to be filled
 * @param {ImportListReportV2Parameters} importParameters  - object comprising all input data
 * @param {string} pageKey - key of the page in manifest
 */
function importTable(
    tableType: v2.TableTypeV2,
    schemaDefinitions: Definition,
    listReportConfig: v2.ListReportConfigV2,
    importParameters: ImportListReportV2Parameters,
    pageKey: string
) {
    let localJsonSchema: Definition;

    if (tableType === 'TreeTable' || tableType === 'AnalyticalTable' || tableType === 'GridTable') {
        localJsonSchema = schemaDefinitions[tableType + '<LineItems>'];
    } else {
        if (schemaDefinitions[MULTISELECTTABLE] && schemaDefinitions[INLINEDELETETABLE]) {
            localJsonSchema = JSON.parse(JSON.stringify(schemaDefinitions[MULTISELECTTABLE])) as Definition;
            localJsonSchema.properties = {
                ...localJsonSchema.properties,
                ...(schemaDefinitions[INLINEDELETETABLE] as Definition).properties
            };
        }
    }
    transferSettingsOfObject(
        listReportConfig.table,
        importParameters.manifest,
        localJsonSchema,
        pageKey,
        importParameters.logger
    );
}

/**
 * Adds manifest settings to the config.json of List Report V2
 * @param {ImportListReportV2Parameters} importParameters  - object comprising all input data
 * @param {ListReportConfig} listReportConfig - the configuration to be filled
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {string} pageKey - key of the page in manifest
 */
function addSettings(
    importParameters: ImportListReportV2Parameters,
    listReportConfig: v2.ListReportConfigV2,
    factory: MetadataInstanceInterface,
    pageKey: string
): void {
    const tableType = determineTableType(importParameters.manifest, pageKey);
    listReportConfig.table = factory.createInstance(PageType.ListReport, tableType, listReportConfig.table);
    const schemaDefinitions = (importParameters.jsonSchema as Definition).definitions as Definition;

    //Import table:
    importTable(tableType, schemaDefinitions, listReportConfig, importParameters, pageKey);

    //Import ToolBar:
    listReportConfig.table.toolBar = factory.createInstance(PageType.ListReport, DefinitionName.ToolBar, {
        actions: {}
    });
    transferSettingsOfObject(
        listReportConfig.table.toolBar,
        importParameters.manifest,
        schemaDefinitions[DefinitionName.ToolBarLR],
        pageKey,
        importParameters.logger
    );
    if (schemaDefinitions[DefinitionName.ActionsLR]) {
        for (const actionProperty in schemaDefinitions[DefinitionName.ActionsLR].properties) {
            const actionKey = convertActionKey(actionProperty);
            listReportConfig.table.toolBar.actions[actionKey] = factory.createInstance(
                PageType.ListReport,
                DefinitionName.Action
            );
            transferSettingsOfObject(
                listReportConfig.table.toolBar.actions[actionKey],
                importParameters.manifest,
                schemaDefinitions[DefinitionName.Action],
                pageKey,
                importParameters.logger
            );
        }
    }

    //Import FilterBar:
    importFilterBar(listReportConfig, factory, importParameters, schemaDefinitions, pageKey);

    //Import Footer:
    listReportConfig.footer = { actions: {} };
    const footerAction = schemaDefinitions[DefinitionName.FooterActionsLR];
    if (footerAction) {
        for (const actionProperty in footerAction.properties) {
            const actionKey = convertActionKey(actionProperty);
            listReportConfig.footer.actions[actionKey] = factory.createInstance(
                PageType.ListReport,
                DefinitionName.FooterAction
            );
            transferSettingsOfObject(
                listReportConfig.footer.actions[actionKey],
                importParameters.manifest,
                schemaDefinitions[DefinitionName.Action],
                pageKey,
                importParameters.logger
            );
        }
    }
}

/**
 * Creates the configuration file content for a list report V2
 * @param {ImportListReportV2Parameters} importParameters  - object comprising all input data
 *
 * @returns {ListReportConfig} - the configuration (JSON) for the list report
 */
export function createListReportConfig(
    importParameters: ImportListReportV2Parameters
): v2.ListReportConfigV2 | undefined {
    // Initialize i18next
    initI18n();

    if (!importParameters.manifest[ManifestSection.generic]) {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOFE'),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.generic]
            }
        });
        return;
    }
    const v2Pages = importParameters.manifest[ManifestSection.generic].pages as v2.SapUiAppPagesV2;
    if (!v2Pages) {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOPAGES', { appId: importParameters.manifest['sap.app']['id'] }),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.generic]
            }
        });
        return;
    }
    const factory = new Factory();
    //Instantiate Page
    const config = factory.createPageInstance(PageType.ListReport);

    const pageKeys: string[] = [];
    const v2Page = findListReportPageV2(importParameters.manifest[ManifestSection.generic].pages, pageKeys);
    if (!v2Page) {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOLR'),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.generic]
            }
        });
        return;
    }
    //Add manifest settings to config
    addSettings(importParameters, config, factory, pageKeys[0]);

    // Add flex changes to config
    importParameters.flex.forEach((change) => {
        if (parseChangeString(change)) {
            addFlex(config, change, importParameters.jsonSchema, importParameters.logger);
        }
    });
    addFragments(config, importParameters.manifest, v2Page, PageType.ListReport, importParameters.fragments);
    removeEmptySettings(config);
    return config;
}
