import type { Manifest, v4 } from '@sap/ux-specification-types';
import { QUICKVARPATH } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getTableSettingsPathForOP } from '../../application';
import {
    exportCreationModeName,
    deleteCreationModeName,
    exportCreationModeCreateAtEnd,
    deleteCreationModeCreateAtEnd,
    exportPersonalizationProperty,
    deletePersonalizationProperty,
    exportPersonalization
} from './Table';

export class TableCreationModeOP implements v4.TableCreationModeOP {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId),
            export: (manifestSection, configObject, key, manifestKey): void => {
                exportCreationModeName(manifestSection, configObject, key, manifestKey);
            },
            delete: (manifestSection, _configObject, _key, manifestKey): void => {
                deleteCreationModeName(manifestSection, manifestKey);
            }
        }
    })
    name?: v4.TableCreationModeType;

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId),
            export: (manifestSection, configObject, key, manifestKey): void => {
                exportCreationModeCreateAtEnd(manifestSection, configObject, key, manifestKey);
            },
            delete: (manifestSection, _configObject, _key, manifestKey): void => {
                deleteCreationModeCreateAtEnd(manifestSection, manifestKey);
            }
        }
    })
    createAtEnd?: boolean;
}

export class PersonalizationTypeOP implements v4.PersonalizationTypeOP {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId), // Here we are using path of parent to have correct value for manifestSection
            export: exportPersonalizationProperty,
            delete: deletePersonalizationProperty
        }
    })
    column?: boolean;

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId), // Here we are using path of parent to have correct value for manifestSection
            export: exportPersonalizationProperty,
            delete: deletePersonalizationProperty
        }
    })
    sort?: boolean;

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId),
            export: exportPersonalizationProperty,
            delete: deletePersonalizationProperty
        }
    })
    filter?: boolean;
}

export class QuickVariantSelectionV4OP implements v4.QuickVariantSelectionV4 {
    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId) + QUICKVARPATH
        }
    })
    paths: v4.AnnotationPathAsObject[];

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId) + QUICKVARPATH
        }
    })
    hideTableTitle?: boolean;

    @syncRule({
        manifest: {
            path: (pageName: string[], manifest: Manifest, sectionId: string): string =>
                getTableSettingsPathForOP(pageName, manifest, sectionId) + QUICKVARPATH
        }
    })
    showCounts?: boolean;
}

export class ObjectPageTable implements v4.ObjectPageTable {
    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    type?: v4.TableTypeV4;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP,
            import: (tableSettings: object): object | undefined => {
                const value: any = tableSettings['creationMode'];
                if (typeof value === 'object') {
                    return value;
                } else if (typeof value === 'string') {
                    return {
                        name: value
                    };
                }
            }
        }
    })
    @validity({
        since: '1.84.0'
    })
    creationMode?: TableCreationModeOP;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP,
            import: (tableSettings: object): object | undefined => {
                const value: any = tableSettings['personalization'];
                if (typeof value === 'object') {
                    return value;
                } else if (typeof value === 'boolean') {
                    return {
                        column: value,
                        sort: value,
                        filter: value
                    };
                }
            },
            export: exportPersonalization
        }
    })
    @validity({
        since: '1.86.0'
    })
    personalization?: PersonalizationTypeOP;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    selectionMode?: v4.SelectionMode;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    selectAll?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    selectionLimit?: number;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    enableExport?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    quickVariantSelection?: QuickVariantSelectionV4OP;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    condensedTableLayout?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.86.0'
    })
    enableFullScreen?: boolean;

    @syncRule({
        manifest: {
            path: getTableSettingsPathForOP
        }
    })
    @validity({
        since: '1.84.0'
    })
    enablePaste?: boolean;
}
