import { parseAndMergeAndConvert } from '../../common/utils';
import { generateObjectPageSchemaV2 } from './objectPage';
import { generateListReportSchemaV2 } from './listReport';
import { generateAnalyticalListReportSchemaV2 } from './analyticalListReport';
import { generateOverviewPageSchemaV2 } from './overviewPage';
import type { ExtensionLogger, GenerateAppSchemaParameters, FileData } from '@sap/ux-specification-types';
import { FioriElementsVersion, TemplateType } from '@sap/ux-specification-types';

/**
 * Generates an app specific schema out of the generic schema.
 * Generic types are replaced by information from the app specific annotations.
 *
 * @param {object} genericSchema - generic JSON schema of an object page
 * @param {string} entitySet - the base entity type of the given page
 * @param {FileData[]} annotations - list of all annotation files (file content plus URI as identifier)
 * @param {object} manifest - manifest.json of the app
 * @param {FileData[]} [fragments] - Array with XML fragments.
 * @param {ExtensionLogger} logger - Logger class for logging messages
 *
 * @returns the app specific JSON schema
 */
export function generateObjectPageInterfaceV2(
    genericSchema: object,
    entitySet: string,
    annotations: FileData[],
    manifest: object,
    fragments?: FileData[],
    logger?: ExtensionLogger
): object {
    const serviceAVT = parseAndMergeAndConvert(annotations, logger);
    const generateParameters: GenerateAppSchemaParameters = {
        templateType: TemplateType.ListReportObjectPageV2,
        fioriElementsVersion: FioriElementsVersion.v2,
        manifest,
        fragments,
        serviceAVT,
        logger
    };
    return generateObjectPageSchemaV2(generateParameters, genericSchema, entitySet);
}

