import 'reflect-metadata';
import type { SyncRule, PageLayoutInformation, ValidityInformation } from '@sap/ux-specification-types';

export const reflectMetadataKey = Symbol('specificationMetadataKey');
export const syncRule = (propSpec: SyncRule): any => {
    return Reflect.metadata(reflectMetadataKey, propSpec);
};

export function getReflectMetadata(target: object, propertyName: string): SyncRule | undefined {
    let rule: SyncRule;
    if (target) {
        rule = Reflect.getMetadata(reflectMetadataKey, target, propertyName);
    }
    return rule;
}

export const pageLayoutInformation =
    (pageInfo: PageLayoutInformation) =>
    (constructor: Function): void => {
        constructor.prototype.pageLayoutInformation = pageInfo;
    };

const childIdKey = Symbol('childId');
export const childId = (id: string): any => {
    return Reflect.metadata(childIdKey, id);
};

export const getChildId = (target: any, propertyName: string): string => {
    return Reflect.getMetadata(childIdKey, target, propertyName);
};

export const getPageLayoutInformation = (target: any): PageLayoutInformation => {
    return Object.getPrototypeOf(target).pageLayoutInformation;
};

/*
 * Validity decorator and functions for it
 */

export const validityMetadataKey = Symbol('validityMetadataKey');
export const validity = (propSpec: ValidityInformation): any => {
    return Reflect.metadata(validityMetadataKey, propSpec);
};

export function getTextForDeprecated(key: string, validityInformation?: ValidityInformation): string {
    let text = `${key} is deprecated`;
    if (validityInformation && validityInformation.deprecated) {
        text = validityInformation.deprecated.since ? `${text} since ${validityInformation.deprecated.since}` : text;
        text = validityInformation.deprecated.text ? `${text}. ${validityInformation.deprecated.text}` : text;
    }

    return text;
}
