import { readFile } from 'fs';
import { join } from 'path';
import type { FioriElementsVersion } from '@sap/ux-specification-types';
import { SchemaType } from '@sap/ux-specification-types';

/**
 * Return the path to the generic JSON schema
 * @param version - Fiori elements version
 * @param schemaType - Type of the schema, e.g Application, ListReport, ObjectPage, ...
 */
export const getPathToGenericSchema = (
    version: FioriElementsVersion,
    schemaType: SchemaType,
    directory: string = __dirname
): string | undefined => {
    const fileName =
        schemaType === SchemaType.Application
            ? `${SchemaType.Application}${version.toUpperCase()}.json`
            : `${schemaType}Config.json`;
    if (directory.endsWith('dist')) {
        return join(directory, 'schemas', version, fileName);
    } else if (directory.endsWith(join('specification', 'packages', 'specification'))) {
        return join(directory, 'dist', 'schemas', version, fileName);
    } else if (directory.endsWith(join('src', 'specification'))) {
        return join(directory, '..', '..', 'dist', 'schemas', version, fileName);
    } else if (directory.indexOf('node_modules') > -1) {
        const moduleRoot = directory.substring(0, directory.indexOf('node_modules') + 12);
        return join(moduleRoot, '@sap', 'ux-specification', 'dist', 'schemas', version, fileName);
    }
    return undefined;
};

/**
 * Read JSON schema from given path
 * @param path - path to schema file
 */
const readJsonSchema = async (path: string): Promise<JSON> => {
    return new Promise((resolve, reject): void => {
        readFile(path, { encoding: 'utf8' }, (err, data): void => {
            if (err) {
                reject(err);
            } else {
                resolve(JSON.parse(data));
            }
        });
    });
};

/**
 * Return the generic schema for a given version and type
 * @param version - Fiori elements version, e.g. v2 or v4
 * @param schemaType - Type of the schema, e.g Application, ListReport, ObjectPage, ...
 */
export const getGenericSchema = async (version: FioriElementsVersion, schemaType: SchemaType): Promise<JSON> => {
    try {
        const path = getPathToGenericSchema(version, schemaType, __dirname);
        return await readJsonSchema(path);
    } catch (error) {
        throw new Error(`No schema available for type: ${schemaType}, version: ${version}. Error was ${error.message}`);
    }
};
