import type { FlexibleColumnLayoutType, ManifestSettingsType } from './types';

export enum PageType {
    ObjectPage = 'ObjectPage',
    ListReport = 'ListReport',
    OverviewPage = 'OverviewPage',
    CustomPage = 'CustomPage',
    AnalyticalListPage = 'AnalyticalListPage'
}

export enum PageTypeV4 {
    ObjectPage = 'ObjectPage',
    ListReport = 'ListReport',
    CustomPage = 'CustomPage',
    FPMCustomPage = 'FPMCustomPage',
    AnalyticalListPage = 'AnalyticalListPage'
}

export const PAGETYPE_VIEW_EXTENSION_TEMPLATE_MAP: Map<PageType, string> = new Map([
    [PageType.ListReport, 'sap.suite.ui.generic.template.ListReport.view.ListReport'],
    [PageType.AnalyticalListPage, 'sap.suite.ui.generic.template.AnalyticalListPage.view.AnalyticalListPage'],
    [PageType.ObjectPage, 'sap.suite.ui.generic.template.ObjectPage.view.Details']
]);

export interface PageConfig {
    $schema?: string;
}

export interface CustomPageConfig extends PageConfig {
    $filePath?: string;
}

export interface PageBase {
    name?: string;
    entitySet?: string;
    navigationProperty?: string;
    config?: PageConfig;
    navigation?: { [property: string]: string | object };
    variantManagement?: string;
    defaultLayoutType?: FlexibleColumnLayoutType;
}

export enum FioriElementsVersion {
    v2 = 'v2',
    v4 = 'v4'
}
export enum OdataVersion {
    v2 = 'v2',
    v4 = 'v4'
}
export interface Target {
    fioriElements: FioriElementsVersion;
    odata: OdataVersion;
}

export interface PagesBase {
    [key: string]: PageBase;
}
export interface SapUiGenericAppPageSettings {
    [key: string]: ManifestSettingsType;
}

export interface SapUiGenericApp {
    pages: SapUiGenericAppPage[] | SapUiGenericAppPageMap;
    _version?: string;
    settings?: object;
}

export interface SapUiGenericAppPage {
    entitySet: string;
    navigationProperty?: string;
    component: {
        name: string;
        settings?: SapUiGenericAppPageSettings;
    };
    pages?: SapUiGenericAppPage[] | { [pageId: string]: SapUiGenericAppPage };
}

export interface SapUiGenericAppPageMap {
    [key: string]: SapUiGenericAppPage;
}

export interface SapUiGenericApp {
    pages: SapUiGenericAppPage[] | SapUiGenericAppPageMap;
    _version?: string;
    settings?: object;
}
