import type { Manifest } from '@sap/ux-specification-types';
import { SAPUI5_FRAGMENT_CLASS, v4 } from '@sap/ux-specification-types';
import { syncRule, validity } from '../../../common/decoration/decorators';
import { getCustomSectionPath } from '../../application';

export class ObjectPageCustomSectionFragment implements v4.ObjectPageCustomSectionFragment {
    @syncRule({
        manifest: {
            path: (entitySet: string[], manifest: Manifest, sectionId): string =>
                getCustomSectionPath(entitySet, manifest, sectionId, 'position'),
            key: 'anchor'
        }
    })
    @validity({
        since: '1.77.0'
    })
    relatedFacet: string;

    @syncRule({
        manifest: {
            path: getCustomSectionPath,
            import: (_manifest: v4.SapUi5RoutingTargetContentSection, sectionId: string): string => {
                // Id is read from path's section - it is object's key
                return sectionId;
            },
            export: false
        }
    })
    @validity({
        since: '1.77.0'
    })
    id: string;

    @syncRule({
        manifest: {
            path: (entitySet: string[], manifest: Manifest, sectionId: string): string =>
                getCustomSectionPath(entitySet, manifest, sectionId, 'position'),
            key: 'placement'
        }
    })
    @validity({
        since: '1.77.0'
    })
    relativePosition: v4.SectionPosition;

    @syncRule({
        manifest: {
            path: getCustomSectionPath
        }
    })
    @validity({
        since: '1.77.0'
    })
    title: string;

    @syncRule({
        manifest: {
            path: getCustomSectionPath,
            import: (manifest: v4.SapUi5RoutingTargetContentSection): v4.CustomSectionViewTypesV4 | string => {
                // Currently only XML supported, but idea is that we need to check 'manifest.type' and allow other types - maybe JS, JSON, etc.
                if (!manifest.type || manifest.type.startsWith(v4.CustomSectionViewTypesV4.XML)) {
                    return v4.CustomSectionViewTypesV4.XML;
                }
                return '';
            },
            export: (manifest: v4.SapUi5RoutingTargetContentSection, object: ObjectPageCustomSectionFragment): void => {
                // Currently only 'XMLFragment', but in future we can add logic here
                let type = '' as typeof v4.SAPUI5_FRAGMENT_TYPE_V4;
                if ('type' in object) {
                    type += object.type;
                }
                if ('className' in object) {
                    if (object.className === SAPUI5_FRAGMENT_CLASS) {
                        type += 'Fragment';
                    } else {
                        type += object.className;
                    }
                }
                manifest.type = type;
            }
        }
    })
    @validity({
        since: '1.77.0'
    })
    type: v4.CustomSectionViewTypesV4;

    @syncRule({
        manifest: {
            path: getCustomSectionPath,
            import: (manifest: v4.SapUi5RoutingTargetContentSection): typeof SAPUI5_FRAGMENT_CLASS | string => {
                // Currently only XML Fragment supported, but idea is that we need to check 'value.type'
                // f.e. type === 'XMLFragment' ? 'sap.ui.core.Fragment' : ...
                if (!manifest.type || manifest.type.startsWith(v4.CustomSectionViewTypesV4.XML)) {
                    if (!manifest.type || manifest.type === 'XMLFragment') {
                        return SAPUI5_FRAGMENT_CLASS;
                    } else {
                        return (manifest.type as string).slice(v4.CustomSectionViewTypesV4.XML.length);
                    }
                }
                // Fallback - if there is unsupported value
                return manifest.type;
            },
            export: false
        }
    })
    @validity({
        since: '1.77.0'
    })
    className: typeof SAPUI5_FRAGMENT_CLASS;

    @syncRule({
        manifest: {
            path: getCustomSectionPath,
            import: (manifestSection: v4.SapUi5RoutingTargetContentSection): string => {
                // During import, we need to consider both `name` and `template` to correctly fill our config and to be upgrade safe
                return manifestSection.name || manifestSection.template;
            },
            key: 'template' // key: 'name' deprecated since '1.86'
        }
    })
    @validity({
        since: '1.77.0'
    })
    fragmentName: string;
}
