import type { v2, ControlTypeFunction } from '@sap/ux-specification-types';
import { ControlType } from '@sap/ux-specification-types';
import { syncRule, exportToFlexChange, buildControlIdFromParent } from '../../../common/decoration';
import { addPatternForBindingChangeOfEnumOP, convertSectionId } from '../../../common';

/**
 * Create stable ID for Object Page Form Field
 *
 * @param {string} baseId Base Id of the control
 * @param {string[]} idBreadcrumbs
 * @param {string[]} breadcrumbs
 * @param {ControlTypeFunction} controlType
 * @param {string} facetID title from the app schema (comprising for instance the facet ID)
 * @returns {string} stable ID of Object Page Form Action button
 */
const getSmartFieldControlId = (
    baseId: string,
    _idBreadcrumbs: string[],
    breadcrumbs: string[],
    _controlType: ControlTypeFunction,
    facetId: string
): string => {
    const convertedId = convertSectionId(breadcrumbs[1], facetId);
    const fieldCrumb = breadcrumbs.slice(-1)[0];
    const breadcrumbsTrail = [convertedId, fieldCrumb, 'GroupElement'];
    return buildControlIdFromParent(baseId, breadcrumbsTrail);
};

export class Field implements v2.Field {
    @syncRule({
        flex: {
            controlId: getSmartFieldControlId,
            controlType: (): ControlType => ControlType.GroupElement,
            createFlexChange: exportToFlexChange
        },
        generate: addPatternForBindingChangeOfEnumOP
    })
    visible?: boolean;
}
