export type ImportResultsV2 =
    | v2.ListReportConfigV2
    | v2.ObjectPageConfigV2
    | v2.OverviewPageConfigV2
    | v2.AnalyticalListPageConfigV2;
export type ImportResultsV4 = v4.ListReportConfigV4 | v4.ObjectPageConfigV4;

export type ImportConfigParameters = ImportParametersV2 | ImportParametersV4;
export type ImportResults = ImportResultsV2 | ImportResultsV4;

/**
 * Types for project import
 */
export interface ImportProjectParameters {
    manifest: Manifest;
    annotations: FileData[];
    flex?: string[];
    fragments?: FileData[];
    logger?: ExtensionLogger;
}

export enum TemplateType {
    ListReportObjectPageV2 = 'ListReportObjectPageV2',
    ListReportObjectPageV4 = 'ListReportObjectPageV4',
    OverviewPageV2 = 'OverviewPageV2',
    AnalyticalListPageV2 = 'AnalyticalListPageV2',
    AnalyticalListPageV4 = 'AnalyticalListPageV4',
    FreestylePageV4 = 'FreestylePageV4'
}

export interface GenerateAppSchemaParameters {
    templateType: TemplateType;
    manifest: Manifest;
    serviceAVT: ConvertedMetadata;
    fioriElementsVersion: FioriElementsVersion;
    entitySet?: EntitySet;
    fragments?: FileData[];
    logger?: ExtensionLogger;
}

export enum CustomExtensionType {
    CustomPage = 'CustomPage',
    CustomColumn = 'CustomColumn',
    CustomSection = 'CustomSection',
    ObjectPage = 'ObjectPage',
    CustomAction = 'CustomAction',
    CustomView = 'CustomView',
    ControllerExtension = 'ControllerExtension'
}

export interface GenerateCustomExtensionParams {
    customExtension: CustomExtensionType;
    basePath: string;
    data: CustomExtensionData;
    fsEditor?: Editor;
}

export type CustomExtensionData =
    | FPMCustomPage
    | FPMCustomTableColumn
    | FPMCustomSection
    | FPMObjectPage
    | FPMCustomAction
    | FPMCustomView
    | FPMControllerExtension;

/**
 * API Version
 */
export interface ApiVersion {
    version?: string;
    fpmWriter?: string;
}

/**
 * API interface
 */
export interface Specification {
    getGenericSchema: (FioriElementsVersion, SchemaType) => Promise<JSON>;
    generateSchema: (GenerateSchemaParameters) => object;
    exportConfig: (ExportConfigParameters) => ExportResults;
    importConfig: (ImportConfigParameters) => ImportResults;
    importProject: (ImportProjectParameters) => Promise<File[]>;
    getApiVersion: () => ApiVersion;
    importProjectSchema: (ImportProjectParameters) => Promise<File[]>;
    generateCustomExtension: (GenerateCustomExtensionParams) => Editor;
}
