import { UIAnnotationTypes } from '@sap-ux/vocabularies-types/vocabularies/UI';

/**
 * Stable Id helper (parts copied from sap.fe.core/helpers/StableIdHelper.ts)
 */

export function replaceSpecialChars(sId: string): string {
    if (typeof sId !== 'string') {
        return;
    }
    sId = sId
        .replace(/^\/|^@|^#|^\*/, '') // remove special characters from the beginning of the string
        .replace(/\/$|@$|#$|\*$/, '') // remove special characters from the end of the string
        .replace(/\/|@|\(|\)|#|\*/g, '::'); // replace special characters with ::

    // Replace double occurrences of the separator with a single separator
    while (sId.indexOf('::::') > -1) {
        sId = sId.replace('::::', '::');
    }

    // If there is a :: at the end of the ID remove it
    if (sId.slice(-2) === '::') {
        sId = sId.slice(0, -2);
    }

    return sId;
}

function removeNamespaces(sId: string): string {
    sId = sId.replace('com.sap.vocabularies.UI.v1.', '');
    sId = sId.replace('com.sap.vocabularies.Communication.v1.', '');
    return sId;
}

export const prepareId = function (sId: string): string {
    return replaceSpecialChars(removeNamespaces(sId));
};

function evaluateConcat(oValue: any) {
    let sPathConcat = '';
    for (const applyValue of oValue.Apply.Path) {
        if (applyValue._text) {
            if (sPathConcat) {
                sPathConcat += '::';
            }
            sPathConcat += applyValue._text;
        }
    }
    return sPathConcat;
}

export function getStableIdPartFromValue(oValue: any): string | undefined {
    if (oValue?.$Path || oValue?.path) {
        return oValue.$Path || oValue.path;
    }

    if (oValue?.Apply?._attributes.Function === 'odata.concat') {
        return evaluateConcat(oValue);
    }

    if (oValue) {
        if (typeof oValue === 'string') {
            return replaceSpecialChars(oValue.replace(/ /g, '_'));
        }
    }
}

export const getStableIdPartFromSemanticObjectAndAction = function (oDataField: any): string {
    let sIdPart = '';
    if (typeof oDataField.SemanticObject === 'string') {
        sIdPart += oDataField.SemanticObject;
    } else if (oDataField.SemanticObject.$Path) {
        sIdPart += oDataField.SemanticObject.$Path;
    }
    if (typeof oDataField.Action === 'string') {
        sIdPart += '::' + oDataField.Action;
    } else if (oDataField.Action && oDataField.Action.$Path) {
        sIdPart += '::' + oDataField.Action.$Path;
    }
    if (oDataField['RequiresContext']) {
        sIdPart += '::RequiresContext';
    }
    return sIdPart;
};

export const getStableIdPartFromDataField = function (oDataField: any): string | undefined {
    let sIdPart = '';

    if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataFieldForAction) {
        sIdPart = 'DataFieldForAction::';
        sIdPart += oDataField.Action;
        return prepareId(sIdPart);
    } else if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataFieldForIntentBasedNavigation) {
        sIdPart = 'DataFieldForIntentBasedNavigation::';
        sIdPart += getStableIdPartFromSemanticObjectAndAction(oDataField);
        return sIdPart;
    } else if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataFieldForAnnotation) {
        sIdPart = 'DataFieldForAnnotation::';
        sIdPart += prepareId(oDataField.Target.$AnnotationPath || oDataField.Target.value);
        return sIdPart;
    } else if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataFieldWithAction) {
        sIdPart = 'DataFieldWithAction::';
        if (oDataField.Value) {
            sIdPart += getStableIdPartFromValue(oDataField.Value) + '::';
        }
        sIdPart += oDataField.Action;
        return prepareId(sIdPart);
    } else if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataField) {
        sIdPart = 'DataField::';
        sIdPart += getStableIdPartFromValue(oDataField.Value);
        return prepareId(sIdPart);
    } else if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataFieldWithIntentBasedNavigation) {
        sIdPart = 'DataFieldWithIntentBasedNavigation::';
        sIdPart += getStableIdPartFromValue(oDataField.Value) + '::';
        sIdPart += getStableIdPartFromSemanticObjectAndAction(oDataField);
        return prepareId(sIdPart);
    } else if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataFieldWithNavigationPath) {
        sIdPart = 'DataFieldWithNavigationPath::';
        sIdPart += getStableIdPartFromValue(oDataField.Value);
        if (oDataField.Target && oDataField.Target['$NavigationPropertyPath']) {
            sIdPart += '::' + oDataField.Target['$NavigationPropertyPath'];
        }
        return prepareId(sIdPart);
    } else if (oDataField.$Type && oDataField.$Type === UIAnnotationTypes.DataFieldWithUrl) {
        sIdPart = 'DataFieldWithUrl::';
        sIdPart += getStableIdPartFromValue(oDataField.Value);
        return prepareId(sIdPart);
    } else {
        // In case of a string or unknown property
        // Log.error("Stable ID Helper: Unable to create a stable ID. Please check the annotations.");
    }
    return undefined;
};
