/**
 * Taken over from https://github.com/sap-staging/ui5-flexibility-utils/blob/origin/master/src/changeUtils.js
 */
export interface ChangeDefinition {
    controlId: string; // ID of the control containing all prefixes excluding the application ID prefix
    controlType: string; // This is NOT in the API so far, but we need it
    type: string; // Change type, i.e. <code>propertyChange</code>
    content: ChangeContent; // Content of the change needed by the corresponding change handler
    isCustomer: boolean; // Flag indicating whether the project is related to a customer project
    sapui5Version: string; // SAPUI5 version for which the change is created
    creatingTool: string; // Name of the tool calling this function for support reasons
    id: string; // UID of the change;
    reference: string; // Reference to the application;
    appVersion: string; // Version of the application;
    creation: string; // String with the creation timestamp;
}
/**
 * Taken over from https://github.com/sap-staging/ui5-flexibility-utils/blob/origin/master/src/changeUtils.js
 */
export interface ChangeDefinitionInCreation {
    controlId: string; // ID of the control containing all prefixes excluding the application ID prefix
    controlType: string; // This is NOT in the API so far, but we need it
    type?: string; // Change type, i.e. <code>propertyChange</code>
    content: ChangeContent; // Content of the change needed by the corresponding change handler
    isCustomer?: boolean; // Flag indicating whether the project is related to a customer project
    sapui5Version?: string; // SAPUI5 version for which the change is created
    creatingTool?: string; // Name of the tool calling this function (this is needed for support reasons)
}

export type CreateFlexChange = (change: ChangeDefinitionInCreation, manifest: Manifest) => string;
export type exportFlexValue = (flexChange: ChangeDefinitionInCreation, configObject: object, key: string) => void;
export type importFlexValue = (flexChange: ChangeDefinitionInCreation, configObject: object, key: string) => void;

export type ControlTypeFunction = () => ControlType;
export type ControlIdFunction = (
    baseId: string,
    idBreadcrumbs: string[],
    breadcrumbs: string[],
    controlType?: ControlTypeFunction,
    title?: string
) => string;

export interface FlexAdapter {
    controlId: ControlIdFunction;
    controlType: ControlTypeFunction;
    createFlexChange: CreateFlexChange;
    exportFlexValue?: exportFlexValue;
    importFlexValue?: importFlexValue;
}

export type ManifestPath = (pageKeys?: string[], manifest?: Manifest, ...pathParts) => string;

export type ExportHandlerFunction = (
    manifestSection: unknown,
    configObject: unknown,
    key: string,
    manifestKey?: string,
    ...pathParts: string[]
) => unknown;

export interface ManifestConverter {
    path: ManifestPath;
    key?: string;
    import?:
        | ((manifestSection: unknown, sectionId?: string, propertyKey?: string, breadcrumbs?: string[]) => unknown)
        | boolean;
    export?: ExportHandlerFunction | boolean;
    delete?:
        | ((
              manifestSection: unknown,
              configObject: unknown,
              key: string,
              manifestKey: string,
              syncRuleKey?: string,
              config?: object
          ) => unknown)
        | boolean;
}

export interface SyncRule {
    flex?: FlexAdapter;
    manifest?: ManifestConverter;
    generate?: (schema: object, definition: object, propertyName: string) => unknown;
}

export interface Content {
    property: string;
    oldValue?: ManifestSettingsType;
    newValue: ManifestSettingsType;
}
