import type { Definition, DefinitionOrBoolean } from 'typescript-json-schema';
import { importProperty, checkDeprecatedProperties, getManifestSectionByPathV4 } from '../../common';
import { getReflectMetadata } from '../../common/decoration/decorators';
import type { ExtensionLogger } from '../../..';
import type { Manifest } from '@sap/ux-specification-types';

/**
 * Transfers settings from manifest to object page config
 * @param configPart - part of the configuration to be filled
 * @param manifest - manifest.json of the app
 * @param appSchema - app specific schema
 * @param pageID - ID of the given object page
 * @param sectionId (optional) - ID of the given section (optional)
 * @param {ExtensionLogger} logger - Logger class for logging messages
 */
export function transferSettingsOfObject(
    configPart: { [key: string]: any },
    manifest: Manifest,
    appSchema: Definition | DefinitionOrBoolean,
    pageKey: string,
    breadcrumbs: string[],
    sectionId?: string,
    logger?: ExtensionLogger
): void {
    if (!appSchema) {
        return;
    }
    let manifestSection = {};
    const deprecatedProperties = configPart?.getDeprecated;
    const targetAnnotationEncoded = sectionId && sectionId.replace('/', '::');

    for (const propertyKey in appSchema['properties']) {
        const syncRule = getReflectMetadata(configPart, propertyKey);

        if (syncRule?.manifest) {
            const path = syncRule.manifest.path([pageKey], manifest, targetAnnotationEncoded, ...breadcrumbs);
            manifestSection = getManifestSectionByPathV4(manifest, path, sectionId, targetAnnotationEncoded);
            if (manifestSection && Object.keys(manifestSection).length > 0) {
                importProperty(syncRule, manifestSection, propertyKey, configPart, sectionId, breadcrumbs);
                checkDeprecatedProperties(deprecatedProperties, manifestSection, path, logger);
            }
        }
    }
}
