import { transferSettingsOfObject, addFlex, determineTableType, addFragments, convertActionKey } from '../common/index';
import type { v2, ImportALPV2Parameters } from '@sap/ux-specification-types';
import { DefinitionName, MANIFESTPATH, ManifestSection, PageType, LogSeverity } from '@sap/ux-specification-types';
import { log } from '../../../../extensionLogger';
import { initI18n } from '../../../../i18n/i18n';
import i18next from 'i18next';
import { parseChangeString } from '../../../common';
import { findAnalyticalListPage, removeEmptySettings } from '../../import/utils';
import { MetadataInstanceFactoryV2 as Factory } from '../../export/factory';
import type { MetadataInstanceInterface } from '../../../common/decoration/factory';
import type { Definition } from 'typescript-json-schema';

/**
 * Adds manifest settings to the config.json
 * @param {AnalyticalListPageConfig} analyticalListPageConfig - the configuration to be filled
 * @param {Manifest} manifest - manifest.json of the app
 * @param {object} jsonSchema - the app-specific schema
 * @param {MetadataInstanceInterface} factory - factory, for generating the access to reflect-metadata
 * @param {string} pageKey - key of the page in manifest
 * @param {ExtensionLogger} logger - instance for logging exceptions
 */
function addSettings(
    importParameters: ImportALPV2Parameters,
    analyticalListPageConfig: v2.AnalyticalListPageConfigV2,
    factory: MetadataInstanceInterface,
    pageKey: string
): void {
    const tableType = determineTableType(importParameters.manifest, pageKey);
    const schemaDefinitions = (importParameters.jsonSchema as Definition).definitions as Definition;

    analyticalListPageConfig.table = factory.createInstance(
        PageType.AnalyticalListPage,
        `ALP${tableType}`,
        analyticalListPageConfig.table
    );
    transferSettingsOfObject(
        analyticalListPageConfig.table,
        importParameters.manifest,
        schemaDefinitions[`ALP${tableType}<LineItems>`],
        pageKey,
        importParameters.logger
    );
    // ToolBar:
    analyticalListPageConfig.table.toolBar = factory.createInstance(
        PageType.AnalyticalListPage,
        DefinitionName.ToolBar,
        {
            actions: {}
        }
    );
    transferSettingsOfObject(
        analyticalListPageConfig.table.toolBar,
        importParameters.manifest,
        schemaDefinitions[DefinitionName.ToolBar],
        pageKey,
        importParameters.logger
    );
    if (schemaDefinitions[DefinitionName.ActionsLR]) {
        for (const actionProperty in schemaDefinitions[DefinitionName.ActionsLR].properties) {
            const actionKey = convertActionKey(actionProperty);
            analyticalListPageConfig.table.toolBar.actions[actionKey] = factory.createInstance(
                PageType.AnalyticalListPage,
                DefinitionName.Action
            );
            transferSettingsOfObject(
                analyticalListPageConfig.table.toolBar.actions[actionKey],
                importParameters.manifest,
                schemaDefinitions[DefinitionName.Action],
                pageKey,
                importParameters.logger
            );
        }
    }
    //FilterBar:
    analyticalListPageConfig.filterBar = factory.createInstance(
        PageType.AnalyticalListPage,
        DefinitionName.AnalyticalListPageFilterBar,
        analyticalListPageConfig.filterBar
    );
    transferSettingsOfObject(
        analyticalListPageConfig.filterBar,
        importParameters.manifest,
        schemaDefinitions[DefinitionName.AnalyticalListPageFilterBar],
        pageKey,
        importParameters.logger
    );
    if (schemaDefinitions[DefinitionName.SelectionFields]) {
        analyticalListPageConfig.filterBar.selectionFields = factory.createInstance(
            PageType.AnalyticalListPage,
            DefinitionName.AnalyticalListPageFilterBar,
            analyticalListPageConfig.filterBar.selectionFields
        );
        const schemaSelectionFields = schemaDefinitions[DefinitionName.SelectionFields].properties;
        for (const selectionProperty in schemaSelectionFields) {
            const selectionKey = selectionProperty;
            analyticalListPageConfig.filterBar.selectionFields[selectionKey] = factory.createInstance(
                PageType.AnalyticalListPage,
                'SelectionFieldDateSettings'
            );
            transferSettingsOfObject(
                analyticalListPageConfig.filterBar.selectionFields[selectionKey],
                importParameters.manifest,
                schemaDefinitions['SelectionFieldDateSettings'],
                pageKey,
                importParameters.logger,
                selectionKey
            );
        }
    }

    //Chart settings:
    analyticalListPageConfig.chart = factory.createInstance(
        PageType.AnalyticalListPage,
        'ChartSettings',
        analyticalListPageConfig.chart
    );
    transferSettingsOfObject(
        analyticalListPageConfig.chart,
        importParameters.manifest,
        schemaDefinitions['ChartSettings'],
        pageKey,
        importParameters.logger
    );
    //Footer:
    analyticalListPageConfig.footer = { actions: {} };
    const footerAction = schemaDefinitions[DefinitionName.FooterActionsLR];
    if (footerAction) {
        for (const actionProperty in footerAction.properties) {
            const actionKey = convertActionKey(actionProperty);
            analyticalListPageConfig.footer.actions[actionKey] = factory.createInstance(
                PageType.AnalyticalListPage,
                DefinitionName.FooterAction
            );
            transferSettingsOfObject(
                analyticalListPageConfig.footer.actions[actionKey],
                importParameters.manifest,
                schemaDefinitions[DefinitionName.Action],
                pageKey,
                importParameters.logger
            );
        }
    }

    //KPI's:
    analyticalListPageConfig.keyPerformanceIndicators = {};
    const kpiCards =
        importParameters.manifest[ManifestSection.generic]['pages'][pageKey].component.settings
            .keyPerformanceIndicators;
    if (kpiCards) {
        Object.keys(kpiCards).forEach((kpiKey) => {
            analyticalListPageConfig.keyPerformanceIndicators[kpiKey] = factory.createInstance(
                PageType.AnalyticalListPage,
                'KPISettings'
            );
            analyticalListPageConfig.keyPerformanceIndicators = factory.createInstance(
                PageType.AnalyticalListPage,
                'KPISettings',
                analyticalListPageConfig.keyPerformanceIndicators
            );
            transferSettingsOfObject(
                analyticalListPageConfig.keyPerformanceIndicators[kpiKey],
                importParameters.manifest,
                schemaDefinitions['KPISettings'],
                pageKey,
                importParameters.logger,
                kpiKey
            );
        });
    }
}

/**
 * Creates the configuration file content for an analytical list page V2
 * @param {ImportALPV2Parameters} importParameters  - object comprising all input data
 *
 * @returns {AnalyticalListPageConfig} - the configuration (JSON)
 */
export function createAnalyticalListPageConfig(
    importParameters: ImportALPV2Parameters
): v2.AnalyticalListPageConfigV2 | undefined {
    // Initialize i18next
    initI18n();

    if (!importParameters.manifest[ManifestSection.generic]) {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOFE'),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.generic]
            }
        });
        return;
    }
    const v2Pages = importParameters.manifest[ManifestSection.generic].pages as v2.SapUiAppPagesV2;
    if (!v2Pages) {
        log(importParameters.logger, {
            severity: LogSeverity.Error,
            message: i18next.t('NOPAGES', { appId: importParameters.manifest['sap.app']['id'] }),
            location: {
                path: MANIFESTPATH,
                range: [ManifestSection.generic]
            }
        });
        return;
    }
    const factory = new Factory();
    //Instantiate Page
    const config = factory.createPageInstance(PageType.AnalyticalListPage);

    const pageKeys: string[] = [];
    const v2Page = findAnalyticalListPage(
        importParameters.manifest[ManifestSection.generic].pages,
        pageKeys,
        importParameters.logger
    );
    if (!v2Page) {
        return;
    }
    addSettings(importParameters, config, factory, pageKeys[0]);
    importParameters.flex.forEach((change) => {
        if (parseChangeString(change)) {
            addFlex(config, change, importParameters.jsonSchema);
        }
    });
    addFragments(config, importParameters.manifest, v2Page, PageType.AnalyticalListPage, importParameters.fragments);
    removeEmptySettings(config);
    return config;
}
