import type { SapUiAppV2 } from '../../../v2/webapp/manifest/sapUiPageV2';
import type { SapUi5 as SapUi5V2 } from '../../../v2/webapp/manifest/sapUi5';
import type { SapUi as SapUiV2 } from '../../../v2/webapp/manifest/sapUi';
import type { SapOvp } from './sapOvp';
import type { SapUi5V4 } from '../../../v4/webapp/manifest/sapUi5';
import type { ManifestSettingsType } from '../../types';

export enum DataSourceType {
    OData = 'OData',
    ODataAnnotation = 'ODataAnnotation'
}

export type ODataVersionType = '2.0' | '4.0';

export interface Inbound {
    signature?: {
        parameters: {};
        additionalParameters: 'allowed';
    };
    semanticObject?: string;
    action?: string;
    title?: string;
    subTitle?: string;
    icon?: '';
}

export interface CrossNavigation {
    inbounds?: {
        [key: string]: Inbound;
    };
}

export interface SapAppSourceTemplate {
    id: string;
    version?: string;
    toolsId?: string;
}

export interface SapApp {
    id?: string;
    title?: string;
    type?: string;
    description?: string;
    dataSources?: SapAppDataSources | SapAppDataSourcesWithOptions;
    offline?: boolean;
    resources?: string;
    sourceTemplate?: SapAppSourceTemplate;
    crossNavigation?: CrossNavigation;
}

export interface SapAppDataSources {
    [key: string]: SapAppDataSource;
    mainService: SapAppDataSource;
}

export interface SapAppDataSource {
    uri: string;
    type: DataSourceType;
    settings: SapAppDataSourceSettings;
}

export interface SapAppDataSourceWithOptions extends SapAppDataSource {
    isLocal: boolean;
    order: number;
    isActive: boolean;
}

export interface SapAppDataSourcesWithOptions {
    [key: string]: SapAppDataSourceWithOptions;
}

export interface SapAppDataSourceSettings {
    [key: string]: ManifestSettingsType;
    annotations?: string[];
    localUri?: string;
    odataVersion?: ODataVersionType;
}

export enum ManifestSection {
    ui = 'sap.ui',
    app = 'sap.app',
    generic = 'sap.ui.generic.app',
    ovp = 'sap.ovp',
    ui5 = 'sap.ui5'
}

export const GENERICAPPSETTINGS = `${ManifestSection.generic}/settings`;

export interface Manifest {
    _version?: string;
    'sap.ui'?: SapUiV2;
    'sap.app'?: SapApp;
    'sap.ui.generic.app'?: SapUiAppV2;
    'sap.ovp'?: SapOvp;
    'sap.ui5'?: SapUi5V2 | SapUi5V4;
}
