import type { ExtensionLogger, LogSeverity, Location } from '@sap/ux-specification-types';
import { LogSeverityLabel } from '@sap/ux-specification-types';

/**
 * Internal representation of one log entry
 */
interface LogEntry {
    severity: LogSeverity;
    message: string;
    location?: Location;
}

/**
 * Function to create a new log entry and add it to the logger.
 * If no logger is passed, puts the message to the console
 * @param logger - the logger instance
 * @param message - the log message
 * @param meta - option list of strings/objects
 */
export const log = (logger: ExtensionLogger, logEntry: LogEntry, ...meta: (object | string | number)[]): void => {
    if (logger) {
        logger[logEntry.severity](logEntry.message, logEntry.location, ...meta);
    } else {
        if (logEntry.location) {
            if (logEntry.location.range) {
                console.log(
                    `${LogSeverityLabel[logEntry.severity]}: ${logEntry.message}, location: ${
                        logEntry.location.path
                    }, ${logEntry.location.range}`,
                    ...meta
                );
            } else {
                console.log(
                    `${LogSeverityLabel[logEntry.severity]}: ${logEntry.message}, location: ${logEntry.location.path}`,
                    ...meta
                );
            }
        } else {
            console.log(`${LogSeverityLabel[logEntry.severity]}: ${logEntry.message}`, ...meta);
        }
    }
};
