export class CommonViewSettings implements v4.CommonViewSettings {
    //key
    @syncRule({
        manifest: {
            path: getSettingsPathForLRViewsPath,
            delete: doNotDelete
        }
    })
    key: string;
    //index
    @syncRule({
        manifest: {
            path: getSettingsPathForLRViewsPaths,
            import: determineIndexOfView,
            export: (manifestSection, configObject): void => {
                checkViewOrder(manifestSection, configObject);
            },
            delete: deleteView
        }
    })
    index: number;

    //annotationPath
    @syncRule({
        manifest: {
            path: getSettingsPathForLRViewsPath,
            delete: doNotDelete
        }
    })
    annotationPath: string;

    //entitySet
    @syncRule({
        manifest: {
            path: getSettingsPathForLRViewsPath
        }
    })
    entitySet?: string;
}

